const getters = {
  getToken () {
    return state.token
  },
  getCurrentPlanetId () {
    const planet = state.planets.find(planet => planet.state === true)
    if (planet) {
      return planet.pid
    }
  },
  getIsInBeginningStory () {
    return state.isInBeginningStory
  },
  getIsBeginner () {
    return state.isBeginner
  },
  getCurrentBeginnerGuideScenes () {
    return state.currentBeginnerGuideScenes
  },
  getDiamond () {
    return state.diamond
  },
  getIsLoading () {
    return state.isLoading
  }
}

const mutations = {
  SET_USER (state, data) {
    Object.keys(data).forEach(key => {
      state[key] = data[key]
    })
  },
  SET_PLANETLIST (state, planets) {
    planets.forEach(planet => {
      planet.pid = planet._id
      planet.scene = 'PlayScene'
    })
    state.planetList = [state.kyronusInfo, ...planets]
  },
  SET_TOKEN (state, token) {
    localStorage.setItem('token', token)
    state.token = token
  },
  COMPLETE_BEGINNINGSTORY (state) {
    state.isInBeginningStory = false
  },
  COMPLETE_BEGINNER (state) {
    state.isBeginner = false
  },
  NEXT_SCENES (state) {
    state.currentBeginnerGuideScenes += 1
  },
  SET_FIRSTREAD (state, index) {
    state.storyMissions[index].firstRead = false
  },
  SPEND_DIAMOND (state, diamond) {
    state.diamond -= diamond
  },
  GAIN_DIAMOND (state, diamond) {
    state.diamond += diamond
  },
  SPEND_COIN (state, coin) {
    state.coin -= coin
  },
  GAIN_COIN (state, coin) {
    state.coin += coin
  },
  SPEND_RESOURCES (state, resources) {
    for (let resource of resources) {
      state.resources.map(userResource => {
        if (userResource.type === resource.type) {
          userResource.amount -= resource.amount
        }
      })
    }
  },
  ADD_RESOURCES (state, resources) {
    for (let resource of resources) {
      state.resources.map(userResource => {
        if (userResource.type === resource.type) {
          userResource.amount += resource.amount
        }
      })
    }
  },
  ADD_COOLDOWNGRID (state, gridData) {
    state.coolDownGrid.push(gridData)
  },
  CHECK_USER_COOLDOWN (state) {
    state.coolDownGrid = state.coolDownGrid.filter(ele => {
      return ele.finishTime >= Date.now()
    })
  },
  SET_ISLOADING (state, boolean) {
    state.isLoading = boolean
  }
}
const actions = {
  async login (context, params) {
    try {
      const result = await axios('/user/login', {
        method: 'post',
        data: JSON.stringify(params)
      })
      context.commit('SET_TOKEN', result.data.token)
      context.commit('SET_USER', result.data)
      return true
    } catch (error) {
      throw error
    }
  },
  async register (context, params) {
    try {
      const result = await axios('/user/register', {
        method: 'post',
        data: JSON.stringify(params)
      })
      context.commit('SET_TOKEN', result.data.token)
      context.commit('SET_USER', result.data)
      return true
    } catch (error) {
      throw error
    }
  },
  async completeBeginningStory (context) {
    try {
      context.commit('COMPLETE_BEGINNINGSTORY')
      await axios('/user/isInBeginningStory', {
        method: 'patch'
      })
    } catch (error) {
      throw error
    }
  },
  async completeBeginner (context) {
    try {
      context.commit('COMPLETE_BEGINNER')
      await axios('/user/isBeginner', {
        method: 'patch'
      })
    } catch (error) {
      throw error
    }
  },
  async nextScenes (context) {
    try {
      context.commit('NEXT_SCENES')
      await axios('user/currentBeginnerGuideScenes', {
        method: 'patch',
        data: JSON.stringify({ currentScenes: context.state.currentBeginnerGuideScenes })
      })
    } catch (error) {
      throw error
    }
  },
  async spendDiamond (context, params) {
    try {
      if (context.state.diamond >= params.diamond) {
        context.commit('SPEND_DIAMOND', params.diamond)
        await axios('/user/diamond', {
          method: 'patch',
          data: JSON.stringify({ diamond: context.state.diamond })
        })
        return true
      } else {
        throw new Error()
      }
    } catch (error) {
      throw error
    }
  },
  async gainDiamond (context, params) {
    try {
      context.commit('GAIN_DIAMOND', params.diamond)
      await axios('/user/diamond', {
        method: 'patch',
        data: JSON.stringify({ diamond: context.state.diamond })
      })
      return true
    } catch (error) {
      throw error
    }
  },
  async spendCoin (context, coin) {
    try {
      context.commit('SPEND_COIN', coin)
      await axios('/user/coin', {
        method: 'patch',
        data: JSON.stringify({ coin: context.state.coin })
      })
      return true
    } catch (error) {
      throw error
    }
  },
  async getUserData (context) {
    try {
      const result = await axios('/user', {
        method: 'get'
      })
      context.commit('SET_USER', result.data)
      return true
    } catch (error) {
      throw error
    }
  },
  async spendResources (context, resources) {
    try {
      context.commit('SPEND_RESOURCES', resources)
      await axios('/user/resources', {
        method: 'patch',
        data: JSON.stringify({ resources: context.state.resources })
      })
      return true
    } catch (error) {
      throw error
    }
  },
  async collectResources (context, resources) {
    try {
      context.commit('ADD_RESOURCES', resources)
      await axios('/user/resources', {
        method: 'patch',
        data: JSON.stringify({ resources: context.state.resources })
      })
    } catch (error) {
      throw error
    }
  },
  async collectCoolDown (context, gridData) {
    try {
      context.commit('ADD_COOLDOWNGRID', gridData)
      await axios('/user/coolDownGrid', {
        method: 'post',
        data: JSON.stringify({ gridData })
      })
    } catch (error) {
      throw error
    }
  },
  async getPlanetList (context, params) {
    try {
      const result = await axios('/user/planetList', {
        method: 'get'
      })
      context.commit('SET_PLANETLIST', result.data)
      return true
    } catch (error) {
      throw error
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
