<template lang="pug">
  div(class="building-upgrade-root")
    div
      h3 {{building.name_zh}}
      p {{building.description}}
      div(class="buff-description")
        div(class="building-image-container")
          img(:src="transformImage.building(building.name_zh)")
        div(class="buff-container")
          div(v-for="item in building.buff")
            span {{item.title}}
            span {{item.value}}
    div(v-if="isArchitectureTechnology && !isMaxLevel" class="can-build")
      div(class="heading-default")
        h4 升級建築
        span {{nextBuilding.name_zh}}
        span 建造時間 1天11:34:20
      div(class="buff-description")
        div(class="building-image-container")
          img(:src="transformImage.building(nextBuilding.name_zh)")
        div(class="buff-container")
          div(v-for="item in nextBuilding.buff")
            span {{item.title}}
            span {{item.value}}
      div(class="resource-group")
        div(v-for="resource in nextBuilding.resources")
          i(:style="{'background-image': `url(${transformImage.things(resource.type)})`}")
          span {{resource.amount}}
      button(class="button-default") 升級該建築
    div(v-else-if="!isArchitectureTechnology && !isMaxLevel" class="not-build")
      div(class="heading-default")
        h4 升級建築
      img(:src="transformImage.building(nextBuilding.name_zh)")
      p 未持有建造技術，請先至研發中心研發
    div(v-else class="max-level")
      div(class="heading-default")
        h4 升級建築
      p 恭喜您，該建築已達最高等級型態
</template>

<script>
import transformImage from '@/assets/javascript/transformImage.js'
import buildingInfo from '@/assets/data/building-info.json'

export default {
  props: {
    building: Object
  },
  data () {
    return {
      transformImage: transformImage,
      nextBuilding: '',
      isArchitectureTechnology: '',
      isMaxLevel: false
    }
  },
  created () {
    if (this.building.nextLevelId === 'none') {
      this.isMaxLevel = true
    }
    const type = this.building.nextLevelId.split('')[0]
    const index = this.building.nextLevelId.split('')[1]
    switch (type) {
      case 'c':
        this.nextBuilding = buildingInfo.commonBuilding[index]
        this.isArchitectureTechnology = this.$store.getters['planet/getArchitectureTechnology'].commonBuilding[this.nextBuilding.id]
        break
      default:
        break
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.building-upgrade-root {
  > div:nth-child(1) {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 24px 16px auto 16px 128px 30px;
    grid-template-areas:
      "name"
      "."
      "description"
      "."
      "buff"
      ".";
    > h3 {
      grid-area: name;
      font-size: 24px;
      line-height: 24px;
      text-shadow: map-get($shadow, secondary-4);
    }
    > p {
      grid-area: description;
      font-size: 11px;
    }
  }
  > .can-build {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 23px 18px 95px 15px 58px 19px 25px;
    grid-template-areas:
      "heading"
      "."
      "buff"
      "."
      "resource"
      "."
      "button";
    > div:nth-child(1) {
      grid-area: heading;
      display: grid;
      grid-template-columns: 64px 1fr 101px;
      grid-column-gap: 12px;
      justify-items: flex-start;
      > span:nth-child(2) {
        align-self: center;
        font-size: 12px;
        text-shadow: map-get($shadow, secondary-4);
      }
      > span:nth-child(3) {
        align-self: center;
        font-size: 10px;
      }
    }
    > .buff-description {
      grid-area: buff;
      grid-template-columns: 85px 166px;
      grid-template-rows: 95px;
    }
    > .resource-group {
      grid-area: resource;
    }
    > button {
      grid-area: button;
      justify-self: center;
      width: 97px;
    }
  }
  > .not-build {
    display: grid;
    grid-template-rows: 23px 18px 100px 22px 12px 24px 25px;
    grid-template-areas:
      "heading"
      "."
      "image"
      "."
      "msg"
      "."
      "button";
    > div:nth-child(1) {
      grid-area: heading;
    }
    > img:nth-child(2) {
      justify-self: center;
      grid-area: image;
      height: 100%;
      opacity: 0.1;
    }
    > p:nth-child(3) {
      grid-area: msg;
      font-size: 12px;
      text-shadow: map-get($shadow, secondary-4);
    }
    > button:nth-child(4) {
      justify-self: center;
      grid-area: button;
      width: 97px;
      opacity: 0.6;
    }
  }
  > .max-level {
    height: 253px;
    display: grid;
    grid-template-rows: 24px 1fr;
    > p {
      align-self: center;
      font-size: 12px;
      text-shadow: map-get($shadow, secondary-4);
    }
  }
  .buff-description {
    justify-self: center;
    grid-area: buff;
    display: grid;
    grid-template-columns: 130px 123px;
    grid-template-rows: 128px;
    grid-column-gap: 14px;
    > .building-image-container {
      > img {
        width: 100%;
      }
    }
    > .buff-container {
      display: grid;
      grid-auto-rows: 26px;
      > div {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        padding: 0 5px;
        border-bottom: 1px solid map-get($colors, primary);
        > span {
          font-size: 10px;
        }
      }
    }
  }
}
</style>
