<template lang="pug">
  custom-transition-group(
    :transitionName="'fade'"
    @transitionEnd="$emit('close')")
    div(slot="transitionContent" :key="'div'")
      video(width="300" height="150" ref="video" playsinline muted)
        source(src="@/assets/video/level-up.webm" type="video/webm")
      p 星球等級已提升至
        strong Level.{{level}}
      p(v-if="planetLevelInfo[level - 1].scale") 星球地盤星球地盤已擴張
      p(v-if="planetLevelInfo[level - 1].diamond") 獲得升等獎勵1顆寶石
</template>

<script>
import CustomTransitionGroup from '@/components/CustomTransitionGroup.vue'
import planetLevelInfo from '@/assets/data/planet-level-info.json'
import { mapGetters, mapActions } from 'vuex'

export default {
  components: {
    CustomTransitionGroup
  },
  data () {
    return {
      module: null,
      planetLevelInfo: planetLevelInfo.result,
      isShowMsg: false
    }
  },
  created: function () {
    import('@/game/scenes/PlayScene').then((module) => {
      this.module = module
    })
  },
  async mounted () {
    if (this.experience < this.planetLevelInfo[this.level - 1].experience) {
      this.$emit('close')
      return
    }
    await this.levelUp()
    await this.sleep(600)
    this.$refs.video.play()
    if (this.planetLevelInfo[this.level - 1].scale) {
      this.expandScale()
        .then(() => this.module.gameFunction.tileScaleUp())
    }
    this.gainDiamond({ diamond: 1 })
    this.gainArchitectureTechnologyPoint({ point: this.planetLevelInfo[this.level - 1].point })
  },
  methods: {
    ...mapActions('user', ['gainDiamond']),
    ...mapActions('planet', ['levelUp', 'expandScale', 'gainArchitectureTechnologyPoint']),
    sleep (time) {
      return new Promise(resolve => setTimeout(resolve, time))
    }
  },
  computed: {
    ...mapGetters('planet', {
      level: 'getPlanetLevel',
      experience: 'getPlanetExperience'
    })
  }
}
</script>

<style lang="scss" scoped>
</style>
