<template lang="pug">
  div(class="buff-card-root" @click="$emit('close')")
    div(class="header")
      div
        h2 {{card.title}}
        button(@click.stop="" class="show-info-btn")
      div
        div
          i(v-for="n in card.rarity" :style="{'background-image': `url(${require('@/assets/icon/star-purple.png')})`}")
        span 擁有數 :
    div(class="main")
      p {{card.description}}
      div
        img(:src="transformImage.things(card.title)")
    div
      div(class="heading-default")
        h4 增減益效果
      div(class="buff-group")
        div(v-for="value,buff in card.buffs")
          span {{buff}}
          span {{value}}
</template>

<script>
import transformImage from '@/assets/javascript/transformImage'

export default {
  components: {
  },
  props: {
    card: Object
  },
  data () {
    return {
      transformImage: transformImage
    }
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.buff-card-root {
  display: grid;
  grid-template-rows: 48px 305px 1fr;
  .header {
    border-bottom: 1px solid map-get($colors, primary);
    > div:nth-child(1) {
      position: relative;
      h2 {
        font-size: 22px;
        line-height: 22px;
        text-shadow: map-get($shadow, secondary-4);
      }
      .show-info-btn {
        top: 0px;
        right: 0px;
        transform: translateY(50%);
        width: 10px;
        height: 10px;
      }
    }
    > div:nth-child(2) {
      position: relative;
      padding: 7px 0;
      display: flex;
      justify-content: center;
      > div {
        display: grid;
        grid-template-columns: 10px 10px 10px;
        grid-column-gap: 3px;
        i {
          width: 10px;
          height: 10px;
          margin-right: 3px;
        }
      }
      span {
        font-size: 10px;
        line-height: 10px;
        position: absolute;
        right: 0px;
      }
    }
  }
  .main {
    display: grid;
    grid-template-rows: 12px 42px 25px 206px 20px;
    grid-template-areas:
      "."
      "description"
      "."
      "img"
      ".";
    p {
      grid-area: description;
      text-align: left;
      letter-spacing: -1px;
      font-size: 10px;
      line-height: 14px;
    }
    div {
      grid-area: img;
      > img {
        height: 100%;
      }
    }
  }
  > div:nth-child(3) {
    .buff-group {
      display: grid;
      grid-template-columns: 136px 136px;
      grid-auto-rows: 27px;
      grid-gap: 11px 7px;
      margin-top: 12px;
      > div {
        box-sizing: border-box;
        border: 1px solid map-get($colors, primary);
        border-radius: 4px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 5px;
        font-size: 10px;
      }
    }
  }
}
</style>
