<template lang="pug">
  div(class="collect-panel-root")
    modal-card(v-if="robotIsOpen" class="modal" @close="robotopen()")
      robot-info(slot="card" @closeRobot="robotopen()" :user="user")
    div(class="user-resources-wrapper")
      div(class="charge-panel")
        i(:style="{'background-image':`url(${require('@/assets/robot-normal.png')})`}" @click="robotopen()")
        span 2
        i
      div
        h6 玩家資源存量
        div
          div(v-for="resource in user.resources.slice(0,4)")
            i(:style="{'background-image': `url(${transformImage.things(resource.type)})`}")
            span {{resource.amount}}
    div(class="button-group")
      i
      i
    div(class="leave")
      router-link(tag="button" to="/game" class="icon-button")
    div(class="collection-info")
      div(v-if="collectionInfo")
        div
          div
            h5 座標 ({{collectionInfo.maxLat}}, {{collectionInfo.maxLng}})
            span(v-if="remainingTime.coolDownTime") 冷卻中 : {{remainingTime.coolDownTime}}
          div(v-if="remainingTime.respawnTime" class="respawn-msg")
            span 波波拉失控區域，等待資源重生中...
            span 重生剩餘時間: {{remainingTime.respawnTime}}
          div(v-else class="collection-resource")
            div(v-for="storage in collectionInfo.storages")
              i(:style="{'background-image': `url(${transformImage.things(storage.type)})`}")
              span {{storage.amount}}
        button(v-if="isCollectable && !remainingTime.coolDownTime &&!remainingTime.respawnTime" class="button-default" @click="$emit('collect')") 進行採集
        button(v-else class="button-default" :style="{'opacity': '0.5'}") 進行採集
      h4(v-else) 點擊地圖框格檢視地表資訊
</template>

<script>
import transformImage from '@/assets/javascript/transformImage.js'
import ModalCard from '@/components/ModalCard.vue'
import RobotInfo from '@/components/game/collect/RobotInfo.vue'

export default {
  props: {
    user: Object,
    collectionInfo: Object,
    isCollectable: Boolean,
    robotOpen: Boolean
  },
  components: {
    ModalCard,
    RobotInfo
  },
  data () {
    return {
      transformImage: transformImage,
      remainingTime: {
        coolDownTime: null,
        respawnTime: null
      },
      coolDownTimer: null,
      respawnTimer: null,
      module: null,
      robotIsOpen: false
    }
  },
  created () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
  },
  beforeDestroy () {
    clearInterval(this.coolDownTimer)
    clearInterval(this.respawnTimer)
  },
  methods: {
    sleep (time) {
      return new Promise(resolve => setTimeout(resolve, time))
    },
    countdown (targetTime, type) {
      const end = Date.parse(new Date(targetTime))
      const now = Date.parse(new Date())
      const msec = end - now
      let day = parseInt(msec / 1000 / 60 / 60 / 24)
      let hr = String(parseInt(msec / 1000 / 60 / 60 % 24)).padStart(2, '0')
      let min = String(parseInt(msec / 1000 / 60 % 60)).padStart(2, '0')
      let sec = String(parseInt(msec / 1000 % 60)).padStart(2, '0')
      this.remainingTime[type] = day > 0 ? `${day}天` : ''
      this.remainingTime[type] += `${hr}:${min}:${sec}`
      if (msec <= 0) {
        clearInterval(this.coolDownTimer)
        clearInterval(this.respawnTimer)
        this.remainingTime[type] = null
      }
    },
    phaserInputEnabled () {
      this.module.gameFunction.scene.input.enabled = true
    },
    robotopen () {
      this.robotIsOpen = !this.robotIsOpen
    }
  },
  watch: {
    collectionInfo: function () {
      if (this.collectionInfo === undefined) {
        return
      }
      clearInterval(this.coolDownTimer)
      clearInterval(this.respawnTimer)
      const coolDownGrid = this.user.coolDownGrid.find(ele => ele.maxLat === this.collectionInfo.maxLat && ele.maxLng === this.collectionInfo.maxLng)
      if (coolDownGrid) {
        this.countdown(coolDownGrid.finishTime, 'coolDownTime')
        this.coolDownTimer = setInterval(this.countdown, 1000, coolDownGrid.finishTime, 'coolDownTime')
      } else {
        this.remainingTime.coolDownTime = null
      }
      if (this.collectionInfo.respawnTime) {
        this.countdown(this.collectionInfo.respawnTime, 'respawnTime')
        this.respawnTimer = setInterval(this.countdown, 1000, this.collectionInfo.respawnTime, 'respawnTime')
      } else {
        this.remainingTime.respawnTime = null
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.collect-panel-root {
  position: fixed;
  z-index: 200;
  .modal {
    width: 100%;
    height: 100%;
    position: fixed;
  }
  .user-resources-wrapper {
    position: fixed;
    top: 40px;
    left: 22px;
    // pointer-events: none;
    > div:nth-child(1) {
      margin-bottom: 10px;
      width: 91px;
      height: 16px;
      > i:nth-child(1) {
        left: 5px;
        top: -20px;
        width: 40px;
        height: 34px;
      }
    }
    > div:nth-child(2) {
      box-sizing: border-box;
      width: 90px;
      height: 162px;
      padding: 9px 8px;
      background-color: rgba($color: map-get($colors, black), $alpha: 0.7);
      border: 1px solid map-get($colors, white);
      border-radius: 4px;
      box-shadow: map-get($shadow, secondary-4);
      > h6 {
        font-size: 11px;
        line-height: 11px;
        padding-bottom: 7px;
        margin-bottom: 11px;
        border-bottom: 0.5px solid map-get($colors, white);
      }
      > div {
        display: grid;
        grid-template-columns: 70px;
        grid-auto-rows: 18px;
        grid-row-gap: 11px;
        justify-content: center;
        > div {
          display: flex;
          justify-content: space-between;
          > i {
            width: 22px;
            height: 18px;
            background-size: contain;
          }
        }
      }
    }
  }
  .button-group {
    display: flex;
    position: fixed;
    top: 36px;
    left: 127px;
    > i {
      width: 24px;
      height: 24px;
      border-radius: 50%;
      box-shadow: map-get($shadow, secondary-4);
      background-color: #fcfcfc;
    }
    > i:nth-child(1) {
      background-image: url("~@/assets/icon/position-flag.png");
      margin-right: 10px;
    }
    > i:nth-child(2) {
      background-image: url("~@/assets/icon/back.png");
    }
  }
  .leave {
    position: fixed;
    top: 23px;
    right: 20px;
    > button {
      display: block;
      width: 37px;
      height: 37px;
      background-image: url("~@/assets/icon/leave-cross.png");
      padding: 0px;
    }
  }
  .collection-info {
    box-sizing: border-box;
    position: fixed;
    bottom: 0px;
    width: 100%;
    height: 155px;
    display: flex;
    align-items: flex-end;
    padding: 0 16px 12px 28px;
    background: linear-gradient(
      180deg,
      rgba(14, 3, 20, 0.09987745098039214) 25%,
      rgba(14, 3, 20, 0.5452556022408963) 50%,
      rgba(14, 3, 20, 0.604079131652661) 75%,
      rgba(14, 3, 20, 1) 100%
    );
    > div {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      > div {
        display: grid;
        grid-row-gap: 12px;
        > div:nth-child(1) {
          display: flex;
          align-items: center;
          position: relative;
          > h5 {
            font-size: 18px;
            line-height: 20px;
          }
          > span {
            position: absolute;
            right: -7px;
            transform: translateX(100%);
            font-size: 12px;
            line-height: 12px;
          }
        }
        > .respawn-msg {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          font-size: 12px;
        }
        > .collection-resource {
          display: grid;
          grid-template-columns: 57px 57px;
          grid-template-rows: 18.5px 18.5px;
          grid-gap: 13.5px 31px;
          > div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            > i {
              width: 22px;
              height: 18px;
              background-size: contain;
            }
            > span {
              font-size: 11px;
              line-height: 11px;
            }
          }
        }
      }
      > button {
        width: 87px;
        height: 25px;
      }
    }
    > h4 {
      font-size: 20px;
      line-height: 20px;
      margin: 106px auto 17px;
    }
  }
}
</style>
