<template lang="pug">
  div(class="robot-root")
    div(class="heading-default")
      h1 Duino
      span 擁有數： {{user.collectorMax}}
    div
      p {{content}}
      img(:src="require('@/assets/robot-normal.png')")
    div
      div(class="heading-default")
        h4 採集器狀態
      div
        div(v-for="(item,index) in user.collectorMax"  v-if="robotwait[index]" class="waiting")
          div(:style="{'background-image': `url(${require('@/assets/robot-normal.png')})`}")
          span 待命中
        div(v-else class="cool-down")
          div(:style="{'background-image': `url(${require('@/assets/robot-cool-down.png')})`}")
          span 冷卻中 {{remainingTime[0]}}
    button(class="button-default") 直接結束冷卻
</template>
<script>
import ModalCardContent from '@/components/ModalCardContent.vue'
import { setInterval, clearInterval } from 'timers'
export default {
  props: {
    user: Object
  },
  components: {
    ModalCardContent
  },
  data: function () {
    return {
      // collectorArr: [],
      remainingTime: [],
      remainingTimer: [],
      finishTime: [],
      robotwait: [],
      robotpage: true,
      content: 'Duino是人類的好朋友，面對嚴峻的波波拉，Duino擁有很好的偵測能力，可以避開危險，找到充分的資源。但是使用Duino需要一定的精神力去控制，因此除非擁有很好的能力，不然要等待一下才能使用他呢。'
    }
  },
  created () {
    let self = this
    console.log(this.user.collectorMax)
    for (var i = 0; i < self.user.collectorMax; i++) {
      self.$set(self.robotwait, i, true)
    }
    console.log(`self.robotwait: ${self.robotwait}`)
    self.user.coolDownGrid.forEach((item, index) => {
      // console.log(`[item, index] ${item}, ${index}`)
      if (item.finishTime === undefined) {
      } else {
        self.countDown(item.finishTime, index)
        self.robotwait[index] = false
        self.timer(item.finishTime, index)
        self.finishTime.push(item.finishTime)
      }
    })
  },
  beforeDestroy () {
    clearInterval(this.remainingTimer[0])
  },
  methods: {
    timer (targetTime, index) {
      let self = this
      self.remainingTimer[index] = setInterval(() => {
        self.countDown(targetTime, index)
      }, 1000)
    },
    countDown (targetTime, index) {
      let self = this
      const end = Date.parse(new Date(targetTime))
      const now = Date.parse(new Date())
      const msec = end - now
      console.log(index)
      if (msec <= 0) {
        // self.robotwait[index] = true
        self.$set(self.robotwait, index, true)
        console.log(self.robotwait[index])
        clearInterval(self.remainingTimer[index])
        self.remainingTimer.map(ele => {
          clearInterval(ele)
        })
        self.remainingTimer[index] = null
        self.remainingTime[index] = null
        console.log(self.remainingTimer)
        return
      }
      if (self.remainingTime[index] === null) {
        self.remainingTime.push()
        self.remainingTimer.push()
      }
      console.log(msec)
      console.log(self.remainingTime)
      let day = parseInt(msec / 1000 / 60 / 60 / 24)
      let hr = String(parseInt(msec / 1000 / 60 / 60 % 24)).padStart(2, '0')
      let min = String(parseInt(msec / 1000 / 60 % 60)).padStart(2, '0')
      let sec = String(parseInt(msec / 1000 % 60)).padStart(2, '0')
      self.remainingTime[index] = day > 0 ? `${day}天` : ''
      self.$set(this.remainingTime, index, `${hr}:${min}:${sec}`)

      // self.$set(self.robotwait, index, true)
    }
  }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.robot-root {
  display: grid;
  grid-template-rows: 11px 37px 252px 15px 141px 26px;
  grid-template-areas:
    "."
    "robot-title"
    "robot-content"
    "."
    "state"
    "button";
  > div:nth-child(1) {
    grid-area: robot-title;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    padding-bottom: 13px;
    > h1 {
      font-size: 24px;
      line-height: 24px;
      text-shadow: map-get($shadow, secondary-4);
    }
    > span {
      font-size: 10px;
      line-height: 10px;
      position: absolute;
      right: 0;
    }
  }
  > div:nth-child(2) {
    grid-area: robot-content;
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-rows: 17px 42px 17px 174px;
    grid-template-areas:
      "."
      "discription"
      "."
      "img";
    > p {
      grid-area: discription;
      font-size: 10px;
    }
    > img {
      grid-area: img;
      height: 178px;
    }
  }
  > div:nth-child(3) {
    grid-area: state;
    > div:nth-child(1) {
      > h4 {
        font-size: 12px;
      }
    }
    > div:nth-child(2) {
      overflow-x: scroll;
      overflow-y: hidden;
      padding-top: 18px;
      display: grid;
      grid-auto-columns: 83px;
      grid-template-rows: 83px;
      grid-column-gap: 10px;
      grid-auto-flow: column;
      align-items: center;
      > div {
        width: 83px;
        height: 81px;
        border: solid 1px map-get($colors, white);
        border-radius: 4px;
        display: grid;
        justify-content: center;
        align-items: center;
        grid-template-rows: 12px 50px 6px 9px 1fr;
        grid-template-areas:
          "."
          "img"
          "."
          "p"
          ".";
        > div {
          grid-area: img;
          height: 50px;
          width: 52px;
          position: relative;
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        > span {
          color: map-get($colors, white);
          grid-area: p;
          font-size: 9px;
        }
      }
      .waiting {
        border: solid 1px map-get($colors, white);
        > span {
          color: map-get($colors, white);
        }
      }
      .cool-down {
        border: solid 1px #ff417c;
        > span {
          color: #ff417c;
        }
      }
    }
  }
  > button:nth-child(4) {
    justify-self: center;
    grid-area: button;
    height: 100%;
    width: 97px;
    font-size: 12px;
  }
}
</style>
