const getters = {
  getMapGridData (state) {
    return state.mapGridData
  },
  getRespawnTime (state) {
    return state.respawnTime
  }
}

const mutations = {
  SET_MAPGRIDDATA (state, data) {
    state.mapGridData = [...data]
  },
  ADD_GRIDDATA (state, gridData) {
    state.mapGridData.push(gridData)
  },
  FILTER_MAPGRIDDATA (state) {
    state.mapGridData = state.mapGridData.filter(ele => {
      const stillHaveStorages = ele.properties.storages.filter(ele => ele.amount > 0).length
      if (stillHaveStorages === 0) {
        return
      }
      return ele.properties.respawnTime === null || ele.properties.respawnTime > Date.now()
    })
  },
  COST_RESOURCES (state, data) {
    const { gridData, resources } = data
    gridData.properties.storages.forEach((storage, index) => {
      if (storage.type === resources[index].type) {
        storage.amount -= resources[index].amount
      }
    })
  },
  RESPAWN_GRIDDATA (state, gridData) {
    gridData.properties.respawnTime = Date.now() + state.respawnTime
  }
}
const actions = {
  async getMapGridData (context) {
    try {
      const result = await axios('/map', {
        method: 'get'
      })
      context.commit('SET_MAPGRIDDATA', result.data)
    } catch (error) {
      throw error
    }
  },
  async costResources (context, params) {
    try {
      context.commit('COST_RESOURCES', params)
      await axios('/map', {
        method: 'post',
        data: JSON.stringify({ gridData: params.gridData })
      })
    } catch (error) {
      throw error
    }
  },
  async respawnGridData (context, gridData) {
    try {
      context.commit('RESPAWN_GRIDDATA', gridData)
      await axios('/map', {
        method: 'patch',
        data: JSON.stringify({ gridData })
      })
    } catch (error) {
      throw error
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
