<template lang="pug">
  div(class="warehouse-root")
    div
      div(class="heading-default")
        h3 資源庫存
    div
      div(class="heading-default")
        h4 寶石金幣
        span 金幣最大容量 {{user.coinMax}}
      div
        div(@click="showMoney(0)")
          i(:style="{'background-image': `url(${require('@/assets/icon/diamond.png')})`}")
          span {{user.diamond}}
        div(@click="showMoney(1)")
          i(:style="{'background-image': `url(${require('@/assets/icon/coin.png')})`}")
          span {{user.coin}}
    div
      div(class="heading-default")
        h4 建築材料
        span 每一資源最大容量 {{user.resourceMax}}
      div
        div(v-for="(resource,index) in user.resources" @click="showDetail(resource,index,true)")
          i(:style="{'background-image':`url(${transformImage.things(resource.type)})`}")
          span {{resource.amount}}
    div
      div(class="heading-default")
        h4 背包物品
      div
        div(
          v-for="(item, index) in user.baggage" @click="showDetail(item,index,false)"
          :key="`baggage-${index}`"
          :style="{'background-image': `url(${transformImage.things(item.name_zh)})`}")
    modal-card-content(v-if="isShowDetail" @close="isShowDetail = !isShowDetail")
      stock-detail(slot="card" :stock="selectedStock" :showItem="showItem" :showBottomInfo="showBottomInfo" :showRarity="showRarity")
</template>
<script>
import transformImage from '@/assets/javascript/transformImage.js'
import ModalCardContent from '@/components/ModalCardContent.vue'
import StockDetail from '@/components/game/warehouse/StockDetail.vue'
import things from '@/assets/data/things-information.json'

export default {
  components: {
    ModalCardContent,
    StockDetail
  },
  props: {
    user: Object
  },
  data () {
    return {
      transformImage: transformImage,
      isShowDetail: false,
      selectedStock: '',
      baggage: [],
      showItem: Object,
      showBottomInfo: true,
      showRarity: false,
      baggageAmount: [0, 0, 0],
      moneyDisappear: false
    }
  },
  created () {
    this.user.baggage.forEach((item, idx) => {
      const index = item.id.split('')[1]
      this.baggageAmount[index] = this.baggageAmount[index] + 1
    })
  },
  methods: {
    showDetail (stock, idx, isResource) {
      if (isResource) {
        this.showItem = things.resource[idx]
        this.showBottomInfo = true

        this.showRarity = false
        this.isShowDetail = true
        this.selectedStock = stock
      } else if (!isResource) {
        const index = this.user.baggage[idx].id.split('')[1]
        this.showItem = things.baggage[index]
        this.showBottomInfo = false

        this.showRarity = true
        this.isShowDetail = true
        this.selectedStock = stock
        this.selectedStock['amount'] = this.baggageAmount[index]
      }
    },
    showMoney (type) {
      this.showItem = things.money[type]
      this.showBottomInfo = false
      this.isShowDetail = true
      if (type === 0) {
        var stock = { type: '克金石', amount: this.user.diamond }
        this.selectedStock = stock
      } else {
        stock = { type: '金幣', amount: this.user.coin }
        this.selectedStock = stock
      }
      this.showRarity = false
      this.moneyDisappear = true
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.warehouse-root {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 50px 96px 176px auto;
  grid-template-areas:
    "heading"
    "diamond"
    "resource"
    "baggage";
  > div:nth-child(1) {
    box-sizing: border-box;
    padding-bottom: 21px;
  }
  > div:nth-child(2) {
    > div:nth-child(1) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      > span {
        font-size: 10px;
      }
    }
    > div:nth-child(2) {
      display: flex;
      padding-top: 20px;
      justify-content: space-evenly;
      > div {
        display: flex;
        align-items: center;
        > i {
          width: 28px;
          height: 34px;
          background-size: cover;
        }
        > span {
          font-size: 12px;
        }
      }
    }
  }
  > div:nth-child(3) {
    > div:nth-child(1) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      > span {
        font-size: 10px;
      }
    }
    > div:nth-child(2) {
      display: grid;
      grid-template-columns: 34px 34px 34px 34px;
      grid-template-rows: 49px 49px;
      grid-gap: 16px 36.5px;
      justify-content: center;
      padding-top: 12px;
      > div {
        i {
          width: 34px;
          height: 36px;
          background-size: contain;
        }
        span {
          display: block;
          font-size: 12px;
          line-height: 12px;
        }
      }
    }
  }
  > div:nth-child(4) {
    > div:nth-child(2) {
      display: grid;
      grid-template-columns: 85px 85px 85px;
      grid-auto-rows: 85;
      grid-gap: 11px 8px;
      padding-top: 17px;
      > div {
        width: 85px;
        height: 85px;
        background-color: #ffffff;
        background-size: cover;
      }
    }
  }
}
</style>
