const transformImage = {
  planet (type) {
    switch (type) {
      case '赫克斯':
        return require('@/assets/image/planet/planet-1.png')
      case '奎奈爾':
        return require('@/assets/image/planet/planet-2.png')
      case '蒂瑪修':
        return require('@/assets/image/planet/planet-1.png')
      case '墨菲比斯':
        return require('@/assets/image/planet/planet-2.png')
      default:
        break
    }
  },
  building (type) {
    switch (type) {
      case '巨石陣傳送門':
        return require('@/assets/image/building/stonePortal/stone-portal.png')
      case '風車':
        return require('@/assets/image/building/windmill.png')
      case '科研中心':
        return require('@/assets/image/building/researchCenter/research-center.png')
      case '太空溫室農場':
        return require('@/assets/image/building/greenHouse/green-house.png')
      case '發電廠':
        return require('@/assets/image/building/powerPlant/power-plant.png')
      case '資源傳輸中心':
        return require('@/assets/image/building/resourceTransfer/resource-transfer.png')
      case '倉庫':
        return require('@/assets/image/building/warehouse/warehouse.png')
      case '住宅':
        return require('@/assets/image/building/houseA/house-a.png')
      case '民宅':
        return require('@/assets/image/building/houseB/house-b.png')
      case '市政中心':
        return require('@/assets/image/building/government/government.png')
      default:
        break
    }
  },
  buildingLocked (type) {
    switch (type) {
      case '科研中心':
        return require('@/assets/image/building/researchCenter/research-center-locked.png')
      case '巨石陣傳送門':
        return require('@/assets/image/building/stonePortal/stone-portal-locked.png')
      case '倉庫':
        return require('@/assets/image/building/warehouse/warehouse-locked.png')
      case '風車':
        return require('@/assets/image/building/windmill-locked.png')
      case '太空溫室農場':
        return require('@/assets/image/building/greenHouse/green-house-locked.png')
      case '發電廠':
        return require('@/assets/image/building/powerPlant/power-plant-locked.png')
      case '資源傳輸中心':
        return require('@/assets/image/building/resourceTransfer/resource-transfer-locked.png')
      case '住宅':
        return require('@/assets/image/building/houseA/house-a-locked.png')
      case '民宅':
        return require('@/assets/image/building/houseB/house-b-locked.png')
      case '市政中心':
        return require('@/assets/image/building/government/government-locked.png')
      default:
        break
    }
  },
  things (type) {
    switch (type) {
      case '金幣':
        return require('@/assets/icon/coin.png')
      case '克金石':
        return require('@/assets/icon/diamond.png')
      case '石頭':
        return require('@/assets/icon/stone.png')
      case '木頭':
        return require('@/assets/icon/wood.png')
      case '鋼鐵':
        return require('@/assets/icon/steel.png')
      case '銅礦':
        return require('@/assets/icon/copper.png')
      case '墨金屬':
        return require('@/assets/icon/ore-1.png')
      case '氦金屬':
        return require('@/assets/icon/ore-2.png')
      case '銝金屬':
        return require('@/assets/icon/ore-3.png')
      case '鍨金屬':
        return require('@/assets/icon/ore-4.png')
      case '薛丁格的貓一般':
        return require('@/assets/cat-normal.png')
      case '薛丁格的貓稀有':
        return require('@/assets/cat-rare.png')
      case '薛丁格的貓史詩':
        return require('@/assets/cat-epic.png')
      case '審判冥王・黑帝斯':
        return require('@/assets/hades.png')
      default:
        break
    }
  },
  archiTech (type) {
    switch (type) {
      case '科研中心':
        return require('@/assets/image/building/researchCenter/intelligence-active.png')
      case '太空溫室農場':
        return require('@/assets/image/building/greenHouse/sickle-active.png')
      case '發電廠':
        return require('@/assets/image/building/powerPlant/power-stone-active.png')
      case '資源傳輸中心':
        return require('@/assets/image/building/resourceTransfer/mechanism-active.png')
      case '倉庫':
        return require('@/assets/image/building/warehouse/box-active.png')
      case '住宅':
        return require('@/assets/image/building/houseA/home-bless-active.png')
      case '市政中心':
        return require('@/assets/image/building/government/zeus-rod-active.png')
      case '外交大使館':
        return require('@/assets/image/building/embassy/friendship-contract-active.png')
      case '觀測站':
        return require('@/assets/image/building/observatory/pledge-active.png')
      default:
        break
    }
  },
  archiTechLocked (type) {
    switch (type) {
      case '科研中心':
        return require('@/assets/image/building/researchCenter/intelligence-inactive.png')
      case '太空溫室農場':
        return require('@/assets/image/building/greenHouse/sickle-inactive.png')
      case '發電廠':
        return require('@/assets/image/building/powerPlant/power-stone-inactive.png')
      case '資源傳輸中心':
        return require('@/assets/image/building/resourceTransfer/mechanism-inactive.png')
      case '倉庫':
        return require('@/assets/image/building/warehouse/box-inactive.png')
      case '住宅':
        return require('@/assets/image/building/houseA/home-bless-inactive.png')
      case '市政中心':
        return require('@/assets/image/building/government/zeus-rod-inactive.png')
      case '外交大使館':
        return require('@/assets/image/building/embassy/friendship-contract-inactive.png')
      case '觀測站':
        return require('@/assets/image/building/observatory/pledge-inactive.png')
      default:
        return require('@/assets/image/building/researchCenter/intelligence-inactive.png')
    }
  },
  planetInfoIcon (type) {
    switch (type) {
      case '星球等級':
        return require('@/assets/icon/level-sword.png')
      case '星球規模':
        return require('@/assets/icon/population.png')
      case '人口數量':
        return require('@/assets/icon/population.png')
      case '戰力指數':
        return require('@/assets/icon/growth-rate.png')
      case '已解鎖建築':
        return require('@/assets/icon/level-sword.png')
      case '已召喚腳色':
        return require('@/assets/icon/level-sword.png')
    }
  }
}

export default transformImage
