<template lang="pug">
  div(class="skills-tree-root")
    table(v-if="treeData.name")
      tr
        td(:colspan="treeData.children ? treeData.children.length * 2 : 1" :class="{ extend: treeData.children && treeData.extend }")
          div(class="node")
            div(class="skill" @click="clickNode(treeData)")
              div(:class="{unlocked: !treeData.isLocked}")
                img(:src="treeData.isLocked ? transformImage.archiTechLocked(treeData.name) : transformImage.archiTech(treeData.name)")
                div(v-if="!treeData.isLocked")
                  span(v-if="treeData.nextLevel === 'none'") M
                  span(v-else) {{treeData.level}}
      tr(v-if="treeData.children && treeData.extend")
        td(v-for="(children, index) in treeData.children" :key="index" colspan="2" class="childLevel")
          skills-tree(:skillsInfo="children" @click-node="clickNode($event)" :planet="planet")
</template>

<script>
import transformImage from '@/assets/javascript/transformImage.js'
export default {
  name: 'SkillsTree',
  props: {
    planet: Object,
    skillsInfo: Object
  },
  data () {
    return {
      transformImage: transformImage,
      treeData: {}
    }
  },
  methods: {
    clickNode (node) {
      if (!node.canOpen) {
        return
      }
      this.$emit('click-node', node)
    },
    showIamge (node) {
      if (node.isLocked) {
        return require('@/assets/image/architectureTechnology/book-inactive.png')
      } else {
        return require('@/assets/image/architectureTechnology/book-active.png')
      }
    }
  },
  watch: {
    skillsInfo: {
      handler: function (Props) {
        const self = this
        let extendKey = function (jsonData) {
          let type = jsonData.id.split('')[0]
          switch (type) {
            case 'c':
              type = 'commonBuilding'
              break
            case 'm':
              type = 'militaryBuilding'
              break
            case 'e':
              type = 'economicBuilding'
              break
            case 's':
              type = 'specialBuilding'
              break
            default:
              break
          }
          const archiTech = self.planet.architectureTechnology
          if (archiTech[type][jsonData.id]) {
            jsonData.isLocked = false
          } else {
            jsonData.isLocked = true
          }
          jsonData.canOpen = false
          if (jsonData.parent === 'root') {
            jsonData.canOpen = true
          }
          if (archiTech[type][jsonData.parent]) {
            jsonData.canOpen = true
          }
          jsonData.extend = (jsonData.extend === void 0 ? true : !!jsonData.extend)
          if (Array.isArray(jsonData.children)) {
            jsonData.children.forEach(c => {
              extendKey(c)
            })
          }
          return jsonData
        }
        if (Props) {
          this.treeData = extendKey(Props)
        }
      },
      immediate: true
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

table {
  width: 100%;
  border-collapse: separate !important;
  border-spacing: 0 !important;
  td {
    position: relative;
    vertical-align: top;
    padding: 0 0 20px 0;
    line-height: 0px;
  }

  .node {
    position: relative;
    box-sizing: border-box;
    > .skill {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 60px;
      height: 60px;
      background-color: #242236;
      margin: auto;
      box-sizing: border-box;
      border-radius: 50%;
      > div {
        position: relative;
        width: 55px;
        height: 55px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          width: 28px;
          // max-height: 30px;
        }
        > div {
          box-sizing: border-box;
          position: absolute;
          bottom: 0px;
          right: 0px;
          width: 18px;
          height: 18px;
          border: 1px solid map-get($colors, secondary);
          background-color: #242236;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          span {
            font-size: 8px;
            line-height: 18px;
            color: map-get($colors, secondary);
          }
        }
      }
      .unlocked {
        border: 1px solid map-get($colors, secondary);
      }
    }
  }

  .extend::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0px;
    height: 20px;
    border-left: 2px solid #707070;
    transform: translateX(-50%);
  }
  .childLevel::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: -10px;
    border-top: 2px solid #707070;
  }

  .childLevel:first-child:after {
    box-sizing: border-box;
    left: 50%;
    height: 10px;
    border: 2px solid #707070;
    border-width: 2px 0 0 2px;
  }
  .childLevel:last-child:after {
    box-sizing: border-box;
    right: 50%;
    height: 10px;
    border: 2px solid #707070;
    border-width: 2px 2px 0 0;
  }
  .childLevel:first-child.childLevel:last-child::after {
    border: none;
  }
}
</style>
