<template lang="pug">
  div(class="list")
    modal-card-content(v-if="buildingpage" @close="buildingpage=!buildingpage")
      building-class(slot="card" :building="selectedBuilding" :buildingInfo="buildings.building")
    div(class="illustration-content")
      div(v-for="(building, index) in buildings.building")
        img(:src="showImage(building)" @click="chooseBuilding(building)")
</template>

<script>
import BuildingClass from '@/components/user/building/BuildingClass.vue'
import ModalCardContent from '@/components/ModalCardContent.vue'
import { mapState } from 'vuex'
import transformImage from '@/assets/javascript/transformImage.js'
import buildingInfo from '@/assets/data/building-info.json'

export default {
  name: 'Illustration',
  components: {
    BuildingClass,
    ModalCardContent
  },
  data: function () {
    return {
      transformImage: transformImage,
      buildingpage: false,
      selectedBuilding: {},
      buildings: {
        building: buildingInfo.commonBuilding
      }
    }
  },
  computed: {
    ...mapState('planet', {
      architectureTechnology: state => state.planet.architectureTechnology
    })
  },
  methods: {
    showImage (building) {
      const type = building.id.split('')[0]
      switch (type) {
        case 'c':
          if (this.architectureTechnology.commonBuilding[building.id] === true) return this.transformImage.building(building.name_zh)
          else return this.transformImage.buildingLocked(building.name_zh)
        default:
          break
      }
    },
    chooseBuilding (building) {
      const type = building.id.split('')[0]
      switch (type) {
        case 'c':
          if (this.architectureTechnology.commonBuilding[building.id] === true) {
            this.selectedBuilding = building
            this.buildingpage = true
          }
          break
        default:
          break
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.list {
  .illustration-content {
    display: grid;
    grid-template-columns: 53px 53px 53px 53px;
    grid-auto-rows: 53px;
    grid-column-gap: 16px;
    grid-row-gap: 15px;
    justify-content: center;
    align-items: flex-end;
    > div {
      > img {
        width: 100%;
      }
    }
  }
}
</style>
