<template lang="pug">
  tab(
    class="building-root"
    :navList="navList"
    @switchMenu="switchMenu")
    div(v-if="haveDevelopedAnyOne" slot="content")
      div(class="selected-building")
        h3 {{selectedBuilding.name_zh}}
        div
          div
            img(:src="transformImage.building(selectedBuilding.name_zh)")
          div
            div
              p {{selectedBuilding.description}}
            div
              div
                span 建造格數
                span {{selectedBuilding.grid.row}}X{{selectedBuilding.grid.column}}
              div
                span 建造時間
                span {{selectedBuilding.buildTime}}
              div
                span 花費金幣
                span {{selectedBuilding.cost}}
        div(class="resource-group")
          div(v-for="resource in selectedBuilding.resources" :class="{ notEnough : notEnough(resource)} ")
            i(:style="{'background-image': `url(${transformImage.things(resource.type)})`}")
            span {{resource.amount}}
        button(@click="onBuildMode(selectedBuilding)" class="button-default" :style="isEnough ? 'opacity: 1' : 'opacity: 0.4'") 建造該建築
      div
        div(v-for="building in buildings[currentOpen]")
          img(
            v-if="planet.architectureTechnology[currentOpen_Eg[currentOpen]][building.id]"
            :src="transformImage.building(building.name_zh)"
            :style="selectedBuilding.name_zh === building.name_zh ? 'opacity: 1' : 'opacity: 0.5'"
            @click="chooseBuilding(building)")
    div(v-else slot="content")
      div(class="unDevelop")
        p 尚未研發任何{{currentOpen.slice(0, 2)}}建築
</template>

<script>
import buildingInfo from '@/assets/data/building-info.json'
import Tab from '@/components/Tab.vue'
import transformImage from '@/assets/javascript/transformImage.js'
import { mapGetters } from 'vuex'

let buildGrid = {
  row: 1,
  column: 1
}

export default {
  components: {
    Tab
  },
  props: {
    user: Object,
    planet: Object
  },
  data () {
    return {
      transformImage: transformImage,
      navList: ['通用建築', '軍事建築', '經濟建築', '特色建築'],
      currentOpen: '通用建築',
      selectedBuilding: {},
      buildings: {
        '通用建築': buildingInfo.commonBuilding,
        '軍事建築': buildingInfo.militaryBuilding,
        '經濟建築': buildingInfo.economicBuilding,
        '特色建築': buildingInfo.specialBuilding
      },
      currentOpen_Eg: {
        '通用建築': 'commonBuilding',
        '軍事建築': 'militaryBuilding',
        '經濟建築': 'economicBuilding',
        '特色建築': 'specialBuilding'
      },
      isEnough: true
    }
  },
  created () {
    this.selectedBuilding = this.buildings['通用建築'][0]
    buildGrid.row = this.selectedBuilding.grid.row
    buildGrid.column = this.selectedBuilding.grid.column
  },
  computed: {
    ...mapGetters('user', {
      isBeginner: 'getIsBeginner',
      currentScene: 'getCurrentBeginnerGuideScenes'
    }),
    haveDevelopedAnyOne () {
      return Object.keys(this.planet.architectureTechnology[this.currentOpen_Eg[this.currentOpen]]).length >= 1
    }
  },
  methods: {
    onBuildMode (selectedBuilding) {
      if (this.isBeginner) {
        if (this.currentScene === 16 && selectedBuilding.name_zh !== '倉庫') { return }

        if (this.currentScene === 37 && selectedBuilding.name_zh !== '資源傳輸中心') { return }

        if (this.currentScene === 67 && selectedBuilding.name_zh !== '住宅') { return }

        if (this.currentScene === 85 && selectedBuilding.name_zh !== '市政中心') { return }
      }
      for (let resource of selectedBuilding.resources) {
        if (this.notEnough(resource)) { return false }
      }
      if (this.user.coin < selectedBuilding.cost) {
        return
      }
      this.$emit('close')
      this.$emit('onBuildMode', selectedBuilding)
    },
    switchMenu (name) {
      this.$bus.$emit('nextScenes', '切換' + name)
      this.currentOpen = name
      this.selectedBuilding = this.buildings[this.currentOpen][0]
      buildGrid.row = this.selectedBuilding.grid.row
      buildGrid.column = this.selectedBuilding.grid.column
    },
    chooseBuilding (building) {
      this.selectedBuilding = building
      buildGrid.row = building.grid.row
      buildGrid.column = building.grid.column
      this.isEnough = true
    },
    notEnough (resource) {
      const userResource = this.user.resources.find(item => item.type === resource.type)
      if (userResource.amount < resource.amount) {
        this.isEnough = false
        return true
      }
      return false
    }
  }
}

export { buildGrid }
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.building-root {
  /deep/ .nav {
    span {
      font-size: 11px;
      line-height: 11px;
    }
  }
  > div:nth-child(2) {
    display: grid;
    grid-template-rows: 344px 93px;
    > .selected-building {
      box-sizing: border-box;
      border-bottom: 1px solid map-get($colors, primary);
      display: grid;
      grid-template-rows: 20px 27px 145px 34px 66px 14px 25px 13px;
      grid-template-areas:
        "title"
        "."
        "building"
        "."
        "resource"
        "."
        "build"
        ".";
      > h3:nth-child(1) {
        grid-area: title;
        font-size: 20px;
        line-height: 20px;
        text-shadow: map-get($shadow, secondary-8);
      }
      > div:nth-child(2) {
        grid-area: building;
        display: grid;
        grid-template-columns: 148px 120px;
        grid-template-rows: 145px;
        grid-column-gap: 11px;
        > div:nth-child(1) {
          display: flex;
          justify-content: center;
          align-items: center;
          > img {
            width: 100%;
          }
        }
        > div:nth-child(2) {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          > div:nth-child(1) {
            > p {
              text-align: justify;
              font-size: 10px;
            }
          }
          > div:nth-child(2) {
            > div {
              display: flex;
              justify-content: space-between;
              padding: 8px 8px 0;
              font-size: 10px;
              border-bottom: 1px solid map-get($colors, primary);
            }
          }
        }
      }
      > div:nth-child(3) {
        grid-area: resource;
      }
      > button:nth-child(4) {
        grid-area: build;
        justify-self: center;
        width: 97px;
        height: 25px;
        font-size: 12px;
      }
    }
    > div:nth-child(2) {
      overflow-x: scroll;
      overflow-y: hidden;
      display: grid;
      grid-auto-columns: 70px;
      grid-template-rows: 93px;
      grid-auto-flow: column;
      grid-column-gap: 10px;
      align-items: flex-end;
      > div {
        > img {
          width: 100%;
        }
      }
      &::-webkit-scrollbar {
        display: none;
        // width: 0 !important;
        -ms-overflow-style: none;
        overflow: -moz-scrollbars-none;
      }
    }
    > .unDevelop {
      display: flex;
      justify-content: center;
      align-items: center;
      > p {
        font-size: 14px;
        text-shadow: map-get($shadow, secondary-4);
      }
    }
  }
}
.notEnough {
  border: 1px solid #ff417c;
  > span {
    color: #ff417c;
  }
}
</style>
