<template lang="pug">
  div(class="storymission-root")
    div(class="mission-wrapper")
      div(class="title")
        div(class="heading-default")
          h4 正在進行
      div(class="content")
        div(v-for="(mission,index) in solvingMission")
          div(class="drop-down-wrapper" :class="{'gradient-primary-bgcolor': solvingMissionOpen[index]}")
            div
              h4 {{mission.name_zh}}
              h5 {{mission.brief}}
            i(:style="{'background-image':`url(${showIcon(mission,index,solvingMissionOpen)})`}" @click="changeWrapper(solvingMissionOpen, index, mission)")
          div(v-if="solvingMissionOpen[index]" class="drop-down-list")
            p {{mission.description}}
            div(class="heading-default")
              h4 完成獎賞
              span +{{mission.experience}} exp
            div(class="prize-content")
              div(v-for="(prize, i) in mission.prize")
                img(:src="transformImage.things(prize.name_zh)")
    div(class="mission-wrapper")
      div(class="title")
        div(class="heading-default")
          h4 已解決任務
      div(class="content")
        div(v-for="(mission, index) in solvedMission")
          div(class="drop-down-wrapper" :class="{'gradient-primary-bgcolor': solvedMissionOpen[index]}")
            div
              h4 {{mission.name_zh}}
              h5 {{mission.brief}}
            i(:style="{'background-image':`url(${showIcon(mission, index, solvedMissionOpen)})`}" @click="changeWrapper(solvedMissionOpen, index, mission)")
          div(v-if="solvedMissionOpen[index]" class="drop-down-list")
            p {{mission.description}}
            div(class="heading-default")
              h4 完成獎賞
              span +{{mission.experience}} exp
            div(class="prize-content")
              div(v-for="(prize, i) in mission.prize")
                img(:src="transformImage.things(prize.name_zh)")
</template>

<script>
import missionInfo from '@/assets/data/mission.json'
import { mapState } from 'vuex'
import transformImage from '@/assets/javascript/transformImage.js'

export default {
  name: 'StoryMission',
  data: function () {
    return {
      transformImage: transformImage,
      solvingMissionOpen: [],
      solvedMissionOpen: [],
      solvingMission: [],
      solvedMission: [],
      firstRead: []
    }
  },
  created () {
    this.checkInfo()
  },
  methods: {
    checkInfo () {
      missionInfo.storyMissions.forEach((mission, index) => {
        if (this.userMissionInfo[index].state === 1) {
          this.solvingMission.push(mission)
          this.solvingMissionOpen.push(false)
        } else if (this.userMissionInfo[index].state === 2) {
          this.solvedMission.push(mission)
          this.solvedMissionOpen.push(false)
        }
        if (this.userMissionInfo[index].firstRead === true) {
          this.firstRead.push(true)
        } else {
          this.firstRead.push(false)
        }
      })
    },
    showIcon (mission, index, missionOpen) {
      const i = mission.id.split('')[1]
      if (missionOpen[index] === true) {
        return require('@/assets/icon/gift.png')
      } else {
        return (this.firstRead[i] === true) ? require('@/assets/icon/planet-Info.png') : require('@/assets/icon/audio-bgm.png')
      }
    },
    changeWrapper (missionOpen, index, mission) {
      this.$set(missionOpen, index, !missionOpen[index])
      const i = mission.id.split('')[1]
      if (this.firstRead[i] === true) {
        this.$store.commit('user/SET_FIRSTREAD', i)
        this.firstRead[i] = false
      }
    }

  },
  computed: {
    ...mapState('user', {
      userMissionInfo: state => state.storyMissions
    })
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.storymission-root {
  display: grid;
  grid-template-rows: auto 15px 1fr;
  grid-template-areas:
    "solving-mission-wrapper"
    "."
    "solved-mission-wrapper";
  > div:nth-child(1) {
    grid-area: solving-mission-wrapper;
  }
  > div:nth-child(2) {
    grid-area: solved-mission-wrapper;
  }
  .mission-wrapper {
    display: grid;
    grid-template-rows: 23px auto;
    grid-row-gap: 16px;
    > .content {
      display: grid;
      grid-template-columns: 267px;
      grid-auto-rows: auto;
      grid-row-gap: 10px;
      justify-content: center;
      > div {
        display: grid;
        grid-auto-rows: 54px 184px;
        grid-template-columns: 267px;
        justify-content: center;
        > .drop-down-wrapper {
          border: 1px solid map-get($colors, primary);
          border-radius: 4px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 13px;
          > div {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            > h4 {
              font-size: 18px;
              text-shadow: map-get($shadow, secondary-4);
            }
            > h5 {
              font-size: 10px;
              color: rgba($color: map-get($colors, white), $alpha: 0.6);
              text-shadow: map-get($shadow, secondary-4);
            }
          }
          > i {
            width: 15px;
            height: 15px;
          }
        }
        .gradient-primary-bgcolor {
          background: map-get($linear-gradient, primary);
        }
        .drop-down-list {
          display: grid;
          grid-template-rows: 23px auto 20px 23px 87px;
          grid-template-columns: 240px;
          grid-template-areas:
            "."
            "brief"
            "."
            "prize-title"
            "prize-content";
          justify-content: center;
          align-items: center;
          border: 1px solid map-get($colors, primary);
          border-top: none;
          border-radius: 0 0 4px 4px;
          > p {
            grid-area: brief;
            font-size: 12px;
            text-align: left;
          }
          .heading-default {
            grid-area: prize-title;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            > span {
              font-size: 11px;
              color: map-get($colors, white);
              line-height: 23px;
            }
          }
          .prize-content {
            grid-area: prize-content;
            display: grid;
            grid-template-columns: 50px 50px 50px 50px;
            grid-auto-rows: 50px;
            grid-column-gap: 9px;
            > div {
              > img {
                width: 100%;
                height: 100%;
              }
            }
          }
        }
      }
    }
  }
}
</style>
