<template lang="pug">
  div(class="tab-root")
    div(class="nav")
      span(v-for="item in navList" @click="switchMenu(item)" :style="item === currentMenu ? 'opacity: 1' : 'opacity: 0.4'") {{item}}
    slot(name="content")
</template>

<script>
export default {
  props: {
    navList: Array
  },
  data () {
    return {
      currentMenu: this.navList[0]
    }
  },
  methods: {
    switchMenu (name) {
      this.currentMenu = name
      this.$emit('switchMenu', name)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.tab-root {
  display: grid;
  grid-template-columns: 279px;
  grid-template-rows: auto 1fr;
  grid-row-gap: 16px;
  > .nav {
    display: flex;
    justify-content: space-around;
    border-bottom: 1px solid map-get($colors, primary);
    > span {
      display: block;
      text-shadow: map-get($shadow, secondary-4);
      padding-bottom: 17px;
    }
  }
}
</style>
