import Phaser from 'phaser'
var wH = window.innerHeight
var wW = window.innerWidth

var emitZone = new Phaser.Geom.Rectangle(-150, 0, wW, wH / 8)
var deadZone = new Phaser.Geom.Rectangle(0, wH, wW, wH / 9)

var EmitterConfig = {
  x: 0,
  y: -100,
  emitZone: {
    type: 'random',
    source: emitZone
  },
  radial: false,
  angle: { min: 0, max: 360 },
  scale: 0.1, // { start, end },
  blendMode: 'ADD', // Phaser.BlendModes
  lifespan: 1500,
  // delay : 500,
  speedX: { min: 200, max: 400 },
  speedY: { min: 500, max: 800 },
  maxVelocityX: 200,
  maxVelocityY: 600,
  frequency: 1500,
  // maxParticles: 10,
  deathZone: {
    type: 'onEnter', // 'onEnter', or 'onLeave'
    source: deadZone
  }
}
export default class Weather {
  constructor (scene) {
    this.scene = scene
  }
  addRain () {
    var particles = this.scene.add.particles('Star')
    particles.createEmitter(EmitterConfig)
    this.scene.WeatherGroup.add(particles)
    // particles.emitters.bringToTop(emitter);
  }
}
