<template lang="pug">
  div(class="hades-temple-root" ref="root")
    video(width="375" height="667" playsinline muted ref="drawCardAnim" id="drawCardAnim" @ended="closeDrawAnim")
      source(src="../../../../assets/video/hades-temple/hades-draw-card.webm" type="video/webm")
    video(width="375" height="667" playsinline autoplay muted)
      source(src="../../../../assets/video/hades-temple/hades-entering.webm" type="video/webm")
    div(class="diamond-charge-wrapper")
      div(class="charge-panel")
        i(:style="{'background-image':`url(${require('@/assets/icon/diamond.png')})`}")
        span {{user.diamond}}
        i
    div(class="leave")
      button(@click="close" class="icon-button")
      span 返回星球
    button(@click="showInfo" class="show-info-btn")
    message(v-if="isShowInfo" :errorMsg="msg")
    modal-card-diamond(v-if="isDrawCard"
      :msg="'請問是否奉獻 requiredDiamond克金石，<br/>進行靈魂審判？'"
      :errorMsg="'剩餘不足 requiredDiamond克金石，<br/>無法進行靈魂審判。'"
      :requiredDiamond="50"
      @cancel="isDrawCard = false"
      @confirm="drawCard")
    div(v-else class="draw-action" @click="decideToDrawCard")
    transition(name="modal")
      modal-card(v-if="isCardGenerate" :haveLeaveButton="false")
        buff-card(:card="card" slot="card" @close="closeCard")
</template>

<script>
import Message from '@/components/ErrorMessage.vue'
import ModalCard from '@/components/ModalCard.vue'
import BuffCard from '@/components/BuffCard.vue'
import ModalCardDiamond from '@/components/ModalCardDiamond.vue'

import { mapGetters, mapActions } from 'vuex'

export default {
  components: {
    Message,
    ModalCard,
    ModalCardDiamond,
    BuffCard
  },
  props: {
    user: Object
  },
  data () {
    return {
      isShowInfo: false,
      msg: '觸摸黑帝斯雕像下方的<strong>遠古銘文</strong><br/>並貢獻寶石即可進行靈魂審判',
      isDrawCard: false,
      isCardGenerate: false,
      card: {
        title: '審判冥王・黑帝斯',
        description: '冥界是所有死者唯一的去處，因此黑帝斯的神話形象雖冷酷，但並無大多宗教神話中的惡神色彩，而是典型的奧林帕斯英挺男子。由於冥府位於地底，黑帝斯也被視為地下礦產的支配者。',
        buffs: {
          '基礎採集能力': '+10%',
          '星球能量數值': '+5%',
          '星球娛樂數值': '-5%'
        },
        rarity: 3
      }
    }
  },
  computed: {
    ...mapGetters('user', {
      isBeginner: 'getIsBeginner',
      currentScene: 'getCurrentBeginnerGuideScenes'
    })
  },
  methods: {
    ...mapActions('planet', ['drawBuffCard', 'gainExperience']),
    showInfo () {
      this.isShowInfo = true
      const self = this
      setTimeout(() => {
        self.isShowInfo = false
      }, 2000)
    },
    decideToDrawCard () {
      if (this.isBeginner) {
        if (this.currentScene !== 96) { return }
        this.$bus.$emit('nextScenes', '抽卡')
      }
      this.isDrawCard = true
    },
    drawCard () {
      if (this.isBeginner) {
        this.$bus.$emit('nextScenes', '確認')
      }

      this.isDrawCard = false
      this.$refs.drawCardAnim.style.display = 'block'
      this.$refs.drawCardAnim.play()
      this.drawBuffCard({ buffCard: this.card })
    },
    close () {
      if (this.isBeginner) {
        if (this.currentScene !== 102) { return }
      }
      this.$bus.$emit('close')
    },
    closeDrawAnim () {
      this.$bus.$emit('nextScenes', '抽卡動畫')

      this.$refs.drawCardAnim.style.display = 'none'
      this.isCardGenerate = true
    },
    closeCard () {
      if (this.isBeginner) {
        if (this.currentScene !== 101) { return }
        this.$bus.$emit('nextScenes', '點選卡片')
      }

      this.isCardGenerate = false
      this.gainExperience(150)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.hades-temple-root {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background: black;
  // background-image: url("~@/assets/image/building/hadesTemple/hades-bg-normal.png");
  #drawCardAnim {
    display: none;
  }
  .diamond-charge-wrapper {
    position: absolute;
    top: 39px;
    left: 22px;
    .charge-panel {
      width: 91px;
      height: 16px;
      > i:nth-child(1) {
        left: 0px;
        top: -17px;
        width: 30px;
        height: 35px;
      }
    }
  }
  .leave {
    position: absolute;
    top: 22px;
    right: 19px;
    > button {
      display: block;
      width: 37px;
      height: 37px;
      background-image: url("~@/assets/icon/leave-cross.png");
      padding: 0px;
    }
    > span {
      font-size: 10px;
    }
  }
  .show-info-btn {
    bottom: 35px;
    right: 21px;
  }
  .draw-action {
    position: fixed;
    top: 460px;
    left: 50%;
    transform: translateX(-50%);
    width: 160px;
    height: 75px;
  }

  .modal-enter-active {
    transition: all 1s;
  }
  .modal-leave-active {
    transition: all 0.5s;
  }
  .modal-enter,
  .modal-leave-to {
    opacity: 0;
  }
}
</style>
