<template lang="pug">
  div(class="collect-root")
    div(v-if="isCatchLocation === 0" id="scan-anim-layer" class="cover-layer")
        video(width="350" height="360" playsinline autoplay muted loop)
          source(src="../assets/video/collect/scanning.webm" type="video/webm")
        div(style="font-weight: 700") 掃描地表資源中...
    //- div(v-else-if="isCatchLocation === -1" id="error-layer" class="cover-layer")
    //-   div 抓不到定位，請檢查裝置定位
    //-   router-link(tag="div" to="/game" style="font-weight: 700") 回到遊戲主畫面
    collect-panel(
      v-else
      @collect="ifCollectable"
      :user="userData"
      :collectionInfo="collectionInfo"
      :isCollectable="isCollectable"
      :style="{'pointer-events': userData.isBeginner ? 'none' : 'all'}"
      :class="{'pointer-active': userData.isBeginner}")
    custom-dialog(
      v-if="userData.isBeginner && isCatchLocation === 1"
      class="dialog"
      :currentScenes="currentScenes"
      :planet="planet"
      @nextScenes="nextScenes")
    div(id="mapid")
    collect-report(v-if="isCollecting" @close="closeReport")
</template>

<script>
import L from 'leaflet'
import CustomDialog from '@/components/beginnerGuide/Dialog.vue'
import CollectPanel from '@/components/game/collect/CollectPanel.vue'
import CollectReport from '@/components/game/collect/CollectReport.vue'
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import beginnerGuideInfo from '@/assets/data/beginner-guide-info.json'

export default {
  name: 'Collect',
  components: {
    CollectPanel,
    CustomDialog,
    CollectReport
  },
  data: function () {
    return {
      isCollecting: false,
      isCatchLocation: 0,
      mymap: null,
      watcher: null,
      options: {
        enableHighAccuracy: false,
        timeout: 10000,
        maximumAge: 0,
        distanceFilter: 1
      },
      lat: null,
      lng: null,
      marker: null,
      geojson: null,
      locationGridArr: [],
      collectionInfo: undefined,
      isCollectable: false,
      module: null,
      robotIsOpen: true,
      actions: beginnerGuideInfo.actions
    }
  },
  beforeMount: function () {
    navigator.geolocation.getCurrentPosition(this.drawMap, this.error, this.options)
    this.watcher = navigator.geolocation.watchPosition(this.update, this.updateError, this.options)
  },
  created: function () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
  },
  beforeDestroy: function () {
    navigator.geolocation.clearWatch(this.watcher)
    this.module.gameFunction.scene.input.enabled = true
  },
  computed: {
    ...mapState('user', {
      userData: state => state
    }),
    ...mapState('planet', {
      planet: state => state.planet
    }),
    ...mapGetters('user', {
      currentScenes: 'getCurrentBeginnerGuideScenes'
    }),
    ...mapGetters('map', {
      mapGridData: 'getMapGridData',
      getRespawnTime: 'getRespawnTime'
    })
  },
  methods: {
    ...mapMutations('user', ['CHECK_USER_COOLDOWN', 'NEXT_SCENES']),
    ...mapActions('user', ['collectCoolDown']),
    ...mapMutations('map', ['FILTER_MAPGRIDDATA', 'ADD_GRIDDATA']),
    ...mapActions('map', ['costResources', 'respawnGridData']),
    drawMap (pos) {
      setTimeout(() => {
        this.isCatchLocation = 1
      }, 2000)

      this.lat = pos.coords.latitude
      this.lng = pos.coords.longitude
      if (this.mymap) { this.mymap.remove() }
      this.mymap = L.map('mapid', {
        center: [this.lat, this.lng],
        zoom: 16,
        zoomControl: false
      })
      L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
        attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
        maxZoom: 18,
        minZoom: 8,
        errorTilrUrl: 'http://bpic.588ku.com/element_pic/16/12/07/706f7ff4f15725b17ba1d30d384e6468.jpg',
        id: 'mapbox.streets',
        accessToken: 'pk.eyJ1IjoibGVva3VvMDcyNCIsImEiOiJjanZ6YzltbXUwcDVoM3pyNmI0aGp5N29tIn0.xjdaery7ZqnxkZhbktidYQ'
      }).addTo(this.mymap)

      let myIcon = L.icon({
        iconUrl: require('@/assets/marker.png'),
        iconSize: [54, 56],
        iconAnchor: [27, 56],
        className: 'marker-icon'
        // popupAnchor: [-3, -76]
        // shadowUrl: 'my-icon-shadow.png',
        // shadowSize: [68, 95],
        // shadowAnchor: [22, 94]
      })

      this.marker = L.marker([this.lat, this.lng], { icon: myIcon }).addTo(this.mymap)
      this.drawGeoJSON()
    },
    error (err) {
      console.warn('ERROR(' + err.code + '): ' + err.message)
      this.isCatchLocation = -1
      // 以下為測試用
      let pos = {
        coords: {
          longitude: 120.21575,
          latitude: 22.99962
        }
      }
      this.drawMap(pos)
    },
    updateError (err) {
      console.warn('ERROR(' + err.code + '): ' + err.message)
    },
    update (pos) {
      this.lat = pos.coords.latitude
      this.lng = pos.coords.longitude
      this.marker.setLatLng([this.lat, this.lng])

      if (this.geojson) {
        this.geojson.addData(this.createVisiableAreaData())
      }
    },
    drawGeoJSON () {
      this.geojson = L.geoJSON(this.createVisiableAreaData(), {
        style: this.style,
        onEachFeature: this.onEachFeature
      }).addTo(this.mymap)

      // 設定時器更新geojson
      for (let i = 0; i < this.locationGridArr.length; i++) {
        this.updateLocalGeoJson(this.locationGridArr[i])
      }
    },
    createVisiableAreaData () {
      if (this.geojson) {
        this.geojson.clearLayers()
        this.collectionInfo = undefined
        this.isCollectable = true
      }
      this.locationGridArr = []
      this.CHECK_USER_COOLDOWN()
      this.FILTER_MAPGRIDDATA()
      let maxLat = Math.ceil(this.lat * 100) / 100
      let maxLng = Math.ceil(this.lng * 100) / 100
      let gap = 0.01
      let range = Math.floor(this.userData.detectRange / 2)

      for (let i = -range; i <= range; i++) {
        for (let j = -range; j <= range; j++) {
          let checkLat = Math.round((maxLat + (i * gap)) * 100) / 100
          let checkLng = Math.round((maxLng + (j * gap)) * 100) / 100
          // 找已儲存陣列中是否有符合的
          const gridData = this.mapGridData.find(ele => {
            return ele.properties.maxLat === checkLat && ele.properties.maxLng === checkLng
          })
          if (gridData) { // Global的mapGridData有該格資料
            this.locationGridArr.push(gridData)
          } else {
            let basicGrid = {
              'type': 'Feature',
              'properties': {
                'maxLat': checkLat,
                'maxLng': checkLng,
                'name': 'test',
                'density': 0,
                'respawnTime': null,
                'storages': [
                  {
                    'type': '木頭',
                    'amount': 2000
                  },
                  {
                    'type': '石頭',
                    'amount': 1600
                  },
                  {
                    'type': '鋼鐵',
                    'amount': 1000
                  },
                  {
                    'type': '銅礦',
                    'amount': 400
                  }
                ]
              },
              'geometry': {
                'type': 'Polygon',
                'coordinates': [
                  [
                    [checkLng, checkLat],
                    [checkLng - gap, checkLat],
                    [checkLng - gap, checkLat - gap],
                    [checkLng, checkLat - gap],
                    [checkLng, checkLat]
                  ]
                ]
              }
            }
            this.locationGridArr.push(basicGrid)
          }
        }
      }
      return this.locationGridArr
    },
    style (feature) {
      let remainStorage = 0
      feature.properties.storages.map(ele => {
        remainStorage += ele.amount
      })
      return {
        fillColor: this.getColor(remainStorage * 100 / 5000),
        weight: 2,
        opacity: 1,
        color: 'white',
        dashArray: '3',
        fillOpacity: 0.2
      }
    },
    getColor (d) {
      return d < 10 ? '#680A64'
        : d < 20 ? '#8A2584'
          : d < 40 ? '#A842A4'
            : d < 60 ? '#C964C5'
              : d < 80 ? '#E881E4'
                : d < 100 ? '#FCA8FA'
                  : '#FEDDFD'
    },
    highlightFeature (e) {
      let maxLat = Math.ceil(this.lat * 100) / 100
      let maxLng = Math.ceil(this.lng * 100) / 100
      let layer = e.target

      if (this.userData.isBeginner) {
        if (!(layer.feature.properties.maxLat === maxLat && layer.feature.properties.maxLng === maxLng)) {
          return
        }
        if (!this.nextScenes('gridData')) {
          return
        }
      }
      this.mymap.fitBounds(e.target.getBounds())
      this.geojson.setStyle({
        weight: 2,
        opacity: 1,
        color: 'white',
        dashArray: '3',
        fillOpacity: 0.2
      })

      layer.setStyle({
        weight: 3,
        color: '#666',
        dashArray: '',
        fillOpacity: 0.3
      })

      if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
        layer.bringToFront()
      }

      if (layer.feature.properties.maxLat === maxLat && layer.feature.properties.maxLng === maxLng) {
        this.isCollectable = true
      } else {
        this.isCollectable = false
      }
      this.collectionInfo = layer.feature.properties
    },
    onEachFeature (feature, layer) {
      layer.on({
        click: this.highlightFeature
      })
    },
    ifCollectable () {
      let maxLat = Math.ceil(this.lat * 100) / 100
      let maxLng = Math.ceil(this.lng * 100) / 100

      this.CHECK_USER_COOLDOWN()
      // 檢查是否有足夠的採集器
      let collectorAmount = this.userData.collectorMax - this.userData.coolDownGrid.length
      if (collectorAmount > 0) {
        console.log('收集器足夠')
        // 判斷該格有沒有在冷卻陣列中
        let status = this.userData.coolDownGrid.some(ele => { return ele.maxLat === maxLat && ele.maxLng === maxLng })
        if (status) { // 該格還在冷卻
          console.log('還在冷卻')
        } else {
          this.collect()
        }
      } else {
        console.log('收集器不夠')
      }
    },
    collect () {
      if (this.userData.isBeginner) {
        if (!this.nextScenes('採集')) {
          return
        }
      }
      // 採集動畫與報告呈現
      this.isCollecting = true

      let maxLat = Math.ceil(this.lat * 100) / 100
      let maxLng = Math.ceil(this.lng * 100) / 100
      // 加到使用者自己冷卻陣列中
      this.collectCoolDown({
        maxLat: maxLat,
        maxLng: maxLng,
        finishTime: Date.now() + 10000
      })
      // 更改資料庫mapGridData資料
      const gridData = this.mapGridData.find(ele => {
        return ele.properties.maxLat === maxLat && ele.properties.maxLng === maxLng
      })
      if (gridData) { // 原本該格有存在資料庫中
        gridData.properties.density++
        this.storageToResource(gridData)
      } else { // 原本沒存在，需要增加新的物件於陣列中，並刪除定量資源
        let gap = 0.01
        let newGrid = {
          'type': 'Feature',
          'properties': {
            'maxLat': maxLat,
            'maxLng': maxLng,
            'name': 'test',
            'density': 0,
            'respawnTime': null,
            'storages': [
              {
                'type': '木頭',
                'amount': 2000
              },
              {
                'type': '石頭',
                'amount': 1600
              },
              {
                'type': '鋼鐵',
                'amount': 1000
              },
              {
                'type': '銅礦',
                'amount': 400
              }
            ]
          },
          'geometry': {
            'type': 'Polygon',
            'coordinates': [
              [
                [maxLng, maxLat],
                [maxLng - gap, maxLat],
                [maxLng - gap, maxLat - gap],
                [maxLng, maxLat - gap],
                [maxLng, maxLat]
              ]
            ]
          }
        }
        newGrid.properties.density++
        this.ADD_GRIDDATA(newGrid)
        this.storageToResource(newGrid)
      }
      this.centerOn() // 回到定位中心
    },
    async storageToResource (gridData) {
      const user = this.userData
      const storages = gridData.properties.storages
      let resources = []
      storages.forEach((storage, index) => {
        let addAmount = user.collectAbility * user.collectWeights[index].weights
        if ((storage.amount - addAmount) < 0) {
          addAmount = storage.amount
        }
        resources.push({
          type: storage.type,
          amount: addAmount
        })
      })
      await this.costResources({ gridData, resources })
      await this.$store.dispatch('user/collectResources', resources)
      this.geojson.addData(this.createVisiableAreaData())
      this.shutdownGrid(gridData)
    },
    shutdownGrid (gridData) {
      let stillHaveStorages = gridData.properties.storages.filter(ele => ele.amount > 0).length
      if (stillHaveStorages === 0) {
        this.respawnGridData(gridData)
        setTimeout(() => {
          this.geojson.addData(this.createVisiableAreaData())
        }, this.getRespawnTime)
      }
    },
    centerOn () {
      this.mymap.panTo([this.lat, this.lng])
    },
    updateLocalGeoJson (data) {
      if (data.properties.respawnTime !== null && data.properties.respawnTime >= Date.now()) {
        console.log('大於')
        let time = data.properties.respawnTime - Date.now()
        setTimeout(() => {
          this.geojson.addData(this.createVisiableAreaData())
        }, time)
      }
    },
    nextScenes (action) {
      console.log(action, this.actions[this.currentScenes])
      if (this.actions[this.currentScenes] === action) {
        if (this.actions[this.currentScenes + 1] === '儲存') {
          this.NEXT_SCENES()
          this.$store.dispatch('user/nextScenes')
          if (this.currentScenes === 57) {
            console.log('here')
            this.$router.push('/beginner_guide')
          }
          return true
        }
        this.NEXT_SCENES()
        return true
      }
      return false
    },
    closeReport (action) {
      this.isCollecting = false
      this.nextScenes(action)
    }
  }
}
</script>

<style lang='scss' scoped>
@import "@/assets/scss/variables.scss";
.collect-root {
  height: 100%;
  width: 100%;
  position: fixed;
  color: black;
  .cover-layer {
    height: 100%;
    width: 100%;
    background: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: map-get($colors, white);
  }
  #mapid {
    height: 100%;
    width: 100%;
    z-index: 100;
    /deep/ .marker-icon {
      top: 0;
      animation: markerMove 1s infinite;
    }
    @keyframes markerMove {
      0% {
        top: 0;
      }
      50% {
        top: -5px;
      }
      100% {
        top: 0;
      }
    }
  }
  .dialog {
    /deep/ .dialog-wrapper {
      background-color: map-get($colors, black);
      span {
        color: map-get($colors, black);
      }
      p {
        color: #ffffff;
      }
    }
  }
  /deep/ .pointer-active {
    .collection-info > div > button:nth-child(2) {
      pointer-events: all;
    }
  }
}
</style>
