<template lang="pug">
  div(class="city-hall-root")
    div(class="heading-default")
      h3 市府安排
    div(class="main")
      p 從黑帝斯聖殿召喚的角色，可安排至執政格中，
        br/ 讓這些角色發揮所長，幫助星球成長。
      draggable(tag="div"
      v-model="planet.activeBuffCard"
      v-bind="dragOptions"
      class="card-wrapper"
      :move="() => true"
      @add="() => $bus.$emit('nextScenes', '拖移')"
      :class="{ active: isDragging }")
        div(v-if="planet.activeBuffCard.length >= 1 && !isDragging")
          img(v-for="(card, index) in planet.activeBuffCard"
            :key="`${card}-${index}`"
            :src="transformImage[card.title]")
        span(v-else-if="isDragging") 請選擇執政人選
        i(v-else
          :style="{'background-image':`url(${require('@/assets/icon/charge.png')})`}")
      span 執政格數：1
    div
      div(class="heading-default")
        h4 執政人選
      draggable(tag="div"
        class="card-group"
        v-model="planet.buffCards"
        v-bind="dragOptions"
        :move="onMove"
        @start="isDragging=true"
        @end="isDragging=false")
        div(v-for="(card, index) in planet.buffCards"
          :style="{'background-image': `url(${transformCardImage[card.title]})`}"
          :key="`${card}-${index}-card-group`")
    //- transition(name="modal")
    //-   modal-card(v-if="isShow" :haveLeaveButton="false")
    //-     buff-card(:card="card" slot="card" @close="isShow = false")
</template>

<script>
import draggable from 'vuedraggable'
import ModalCard from '@/components/ModalCard.vue'
import BuffCard from '@/components/BuffCard.vue'
import { mapActions } from 'vuex'

export default {
  components: {
    draggable,
    ModalCard,
    BuffCard
  },
  props: {
    user: Object,
    planet: Object
  },
  data () {
    return {
      transformCardImage: {
        '審判冥王・黑帝斯': require('@/assets/hades-portrait.png')
      },
      transformImage: {
        '審判冥王・黑帝斯': require('@/assets/hades.png')
      },
      editable: true,
      isDragging: false,
      delayedDragging: false
    }
  },
  methods: {
    ...mapActions('planet', ['useBuffCard']),
    onMove ({ relatedContext, draggedContext }) {
      const self = this
      if (relatedContext.component.$el.matches('.card-wrapper')) {
        this.planet.activeBuffCard = this.planet.activeBuffCard.filter(item => {
          self.planet.buffCards.push(item)
        })
      }
      return true
    },
    onWithdraw (relatedContext, originElement) {
      return true
    }
  },
  computed: {
    dragOptions () {
      return {
        animation: 0,
        group: 'description',
        ghostClass: 'ghost'
      }
    }
  },
  watch: {
    isDragging (newValue) {
      if (newValue) {
        this.delayedDragging = true
        return
      }
      this.$nextTick(() => {
        this.delayedDragging = false
      })
    }
  },
  beforeDestroy () {
    this.useBuffCard({ activeBuffCard: this.planet.activeBuffCard })
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.city-hall-root {
  display: grid;
  grid-template-rows: 30px 274px 1fr;
  .main {
    display: grid;
    grid-template-rows: 15px 31px 16px 180px 10px 11px 10px;
    grid-template-areas:
      "."
      "description"
      "."
      "img"
      "."
      "amount"
      ".";
    > p {
      grid-area: description;
      font-size: 11px;
      line-height: 15px;
    }
    > .card-wrapper {
      grid-area: img;
      justify-self: center;
      width: 110px;
      border-radius: 6px;
      border: 1px solid map-get($colors, white);
      border-style: dashed;
      display: flex;
      align-items: center;
      justify-content: center;
      > i {
        width: 35px;
        height: 35px;
        background-size: contain;
      }
      > div {
        width: 100%;
        height: 100%;
        padding: 5px;
        box-sizing: border-box;
        > img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .active {
      background: linear-gradient(
        360deg,
        rgba(73, 103, 219, 1) 0%,
        rgba(170, 105, 181, 1) 100%
      );
      > span {
        font-size: 12px;
      }
    }
    > span {
      grid-area: amount;
      font-size: 11px;
      line-height: 11px;
    }
    .ghost {
      display: none;
    }
  }
  > div:nth-child(3) {
    .card-group {
      margin-top: 17px;
      display: grid;
      justify-content: center;
      grid-template-columns: 55px 55px 55px 55px;
      grid-auto-rows: 55px;
      grid-gap: 10px 19px;
      > div {
        background-size: cover;
      }
    }
  }
}
</style>
