<template lang="pug">
  div(class="building-root")
    div(class="title")
      h1 {{building.name_zh}}
    div(class="brief")
      div(class="brief-building")
        p(class="building-intro") {{building.description}}
      div
        img(v-bind:src="transformImage.building(building.name_zh)")
      span 類別：{{typeName}}
    div(class="heading-default")
      h4 建築升級
    div(class="advance-brief")
      div(v-for="(building, index) in buildingClass")
        img(v-bind:src="transformImage.buildingLocked(building.name_zh)")
</template>
<script>
import transformImage from '@/assets/javascript/transformImage.js'

export default {
  name: 'BuildingClass',
  props: {
    building: Object,
    buildingInfo: Array
  },
  data: function () {
    return {
      transformImage: transformImage,
      typeName: '',
      buildingClass: []
    }
  },
  created () {
    this.checkNextBuilding(this.building)
    this.showType(this.building)
  },
  methods: {
    showType (building) {
      const type = this.building.id.split('')[0]
      switch (type) {
        case 'c':
          this.typeName = '基本建築'
          break
        default:
          break
      }
    },
    checkNextBuilding (building) {
      if (building.nextLevelId === 'none') {
        this.buildingClass.push(building)
        return
      }
      const index = building.nextLevelId.split('')[1]
      const nextBuilding = this.buildingInfo[index]
      this.buildingClass.push(nextBuilding)
      this.checkNextBuilding(nextBuilding)
    }
  }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.building-root{
  display: grid;
  grid-template-rows:37px 316px 32px 90px;
  grid-template-areas:
  "title"
  "brief"
  "advance-title"
  "class-img";
  .title {
    grid-area: title;
    border-bottom: 1px solid map-get($colors, primary);
    >h1 {
      font-size: 24px;
    }
  }
  .brief {
    grid-area: brief;
    display: grid;
    justify-content: center;
    align-content: center;
    grid-template-rows: 17px 42px 17px 173px 19px 10px 38px;
    grid-template-areas:
    "."
    "brief-windmill"
    "."
    "windmill-pic"
    "."
    "category"
    ".";
    .brief-building{
      grid-area: brief-windmill;
      .building-intro {
        font-size: 10px;
      }
    }
    > div:nth-child(2) {
      grid-area: windmill-pic;
      >img {
        width:175px;
        height:auto;
      }
    }
    >span {
      grid-area: category;
      font-size: 10px;
    }
  }
  .heading-default {
    grid-area: advance-title;
    >h4 {
      font-size: 18px;
      padding: 7px 10px;
      line-height: 18px;
    }
  }
  .advance-brief {
    grid-area: class-img;
    display: grid;
    justify-content: center;
    grid-template-columns: 66px 66px 66px;
    grid-auto-rows: 66px;
    grid-column-gap: 29px;
    //grid-row-gap: 24px;
    padding-top: 24px;
    >div {
      >img {
        width: 100%;
      }
    }
  }
}
</style>
