<template lang="pug">
  div(id="list-content")
    div(v-for="planet in planetList")
      div(:style="{'background-image': `url(${transformImage.planet(planet.type)})`}")
      div
        div
          h4 {{planet.name}}
        div
          i
          span ({{planet.positionX}}, {{planet.positionY}})
        div
          h6 等級 Lv.{{planet.level}}
          h6(v-if="planet.experience") {{planet.experience}} / {{planetLevelInfo[planet.level].experience}}
        div(v-if="planet.experience")
          div(v-bind:style="{width: level(planet.experience, planetLevelInfo[planet.level].experience) + '%'}")
      div(v-if="user.isOnMainPlanet")
        div(v-if="planet.scene === 'HumanMainScene'" class="use")
          span 目前
        div(v-else @click="switchPlanet(planet.scene)")
          span 前往
      div(v-else)
        div(v-if="planet.scene === 'PlayScene'" class="use")
          span 目前
        div(v-else @click="switchPlanet(planet.scene)")
          span 前往
</template>

<script>
import { mapState } from 'vuex'
import transformImage from '@/assets/javascript/transformImage'
import planetLevelInfo from '@/assets/data/planet-level-info.json'

export default {
  props: {
    planetList: Array
  },
  data: function () {
    return {
      module: null,
      moduleMain: null,
      transformImage: transformImage,
      planetLevelInfo: planetLevelInfo.result
    }
  },
  beforeCreate () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
    import('@/game/scenes/HumanMainScene').then(async (module) => {
      this.moduleMain = module
    })
  },
  methods: {
    level: function (experience, nextLevel) {
      return 100 * (experience / nextLevel)
    },
    switchPlanet (target) {
      if (this.user.isOnMainPlanet) {
        this.moduleMain.gameFunction.changeScene(target)
      } else {
        this.module.gameFunction.changeScene(target)
      }
      this.$bus.$emit('close')
      this.user.isOnMainPlanet = !this.user.isOnMainPlanet
    }
  },
  computed: {
    ...mapState('user', {
      user: state => state
    })
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

#list-content {
  display: grid;
  grid-template-columns: 267px;
  grid-auto-rows: 90px;
  grid-row-gap: 14px;
  justify-content: center;
  > div {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    border: 1px solid map-get($colors, primary);
    box-shadow: 0 0 2px map-get($colors, primary);
    padding: 7px 9px;
    > div:nth-child(1) {
      width: 76px;
      height: 76px;
      background-size: contain;
      background-position: center;
    }
    > div:nth-child(2) {
      height: 100%;
      width: 107px;
      display: grid;
      grid-template-rows: 10px 12px 12px 10px 8px 9px 5px 4px 6px;
      grid-template-areas:
        "."
        "name"
        "."
        "position"
        "."
        "level"
        "."
        "experience"
        ".";
      > div:nth-child(1) {
        grid-area: name;
        text-align: left;
        > h4 {
          font-size: 12px;
          line-height: 12px;
        }
      }
      > div:nth-child(2) {
        grid-area: position;
        display: flex;
        > i {
          width: 6px;
          height: 9px;
          background-image: url("~@/assets/icon/planet-locate.png");
          margin-right: 5px;
        }
        > span {
          font-size: 9px;
          line-height: 9px;
        }
      }
      > div:nth-child(3) {
        grid-area: level;
        display: flex;
        justify-content: space-between;
        > h6 {
          font-size: 9px;
          line-height: 9px;
        }
      }
      > div:nth-child(4) {
        grid-area: experience;
        background-color: map-get($colors, white);
        height: 4px;
        > div {
          height: 4px;
          background: map-get($linear-gradient, primary);
        }
      }
    }

    > div:nth-child(3) {
      > div {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 3px;
        height: 18px;
        width: 43px;
        border: 1px solid map-get($colors, primary);
      }
      span {
        font-size: 10px;
        line-height: 16px;
      }
      .use {
        background: map-get($linear-gradient, primary);
        border: none;
      }
    }
  }
}
</style>
