import { Scene } from 'phaser'
import Group from 'phaser/src/gameobjects/group/Group'
import Weather from '@/game/Effect/StarRain'

export var PlanetGameFunc = {}

export default class PlanetScene extends Scene {
  constructor () {
    super('PlanetScene')
  }
  init () {
    var self = this
    // GameObj = new GameObjPrototype(this);
    PlanetGameFunc.SwitchPlanet = function (key) {
      console.log(self.PlanetLightTween)
      // self.PlanetLightTween.remove()
      self.tweens.add({
        targets: [self.planet, self.planetLight],
        duration: 500,
        alpha: 0,
        ease: 'Quad.easeOut'
      })
      var wH = window.innerHeight
      var wW = window.innerWidth

      self.planet.destroy()
      self.planetLight.destroy()
      self.planet = self.add.sprite(wW / 2, wH / 2.35, 'planet-2')
    }
    PlanetGameFunc.ChangeScene = function () {
      self.scene.transition({
        target: 'PlayScene',
        duration: 0,
        sleep: true
      })
    }
    PlanetGameFunc.CameraFadeIn = function () { // 用在重新回到該頁面時的動畫
      self.cameras.main.fadeIn(2000)
    }

    this.weather = new Weather(this)
    this.backgroundGroup = new Group(this.game, undefined, 'Backgrond')
    this.buildGroup = new Group(this.game, undefined, 'Building')
    this.WeatherGroup = new Group(this.game, undefined, 'Weather')
    this.star = []
  }
  create () {
    console.log('PlanetScene create')
    var wH = window.innerHeight
    var wW = window.innerWidth

    this.background = this.add.tileSprite(wW / 2, wH / 2, wW, wH, 'bg')
    this.weather.addRain()

    this.planetLight = this.add.sprite(wW / 2, wH / 2.35, 'planet-1-light')
    this.planet = this.add.sprite(wW / 2, wH / 2.35, 'planet-1')

    this.planetLight.alpha = 0.6

    this.PlanetLightTween = this.tweens.add({
      targets: [this.planetLight],
      duration: 1700,
      alpha: 1,
      ease: 'Quad.easeOut',
      repeat: -1,
      yoyo: true
    })

    this.star[0] = this.add.sprite(0.2 / 4 * wW, 1 / 7 * wH, 'Star')
    this.star[1] = this.add.sprite(0.5 / 4 * wW, 2 / 7 * wH, 'Star')
    this.star[2] = this.add.sprite(3.6 / 4 * wW, 1.2 / 7 * wH, 'Star')
    this.star[3] = this.add.sprite(1.1 / 5 * wW, 5.5 / 7 * wH, 'Star')
    this.star[4] = this.add.sprite(4.4 / 5 * wW, 6.1 / 7 * wH, 'Star')
    for (var i = 0; i < 5; i++) {
      this.star[i].scaleX = 0.1 + i / 50
      this.star[i].scaleY = 0.1 + i / 50
      this.star[i].alpha = 0.3 + i / 10
      this.tweens.add({
        targets: this.star[i],
        duration: 2000 + i * 200,
        alpha: 1,
        ease: 'Quad.easeOut',
        repeat: -1,
        yoyo: true
      })
    }

    this.cameras.main.fadeIn(2000)
    // this.EnterPlanet()
    console.log('PS')
    var bgm = this.sound.add('bgm')
    bgm.loop = Infinity
    bgm.play()
  }
  update () {
    // this.planet.angle += 0.3
    this.background.tilePositionX += 0.1
  }
  EnterPlanet () {
    console.log(PlanetScene)
    this.cameras.main.fadeOut(2000)
    this.cameras.main.zoomTo(2.5, 3000)
  }
}

