<template lang="pug">
  div(class="planet-info-level-root")
    h3 星球等級
    div
      div
        img(:src="require('@/assets/image/planet/info-planet.png')")
      span Level. {{planet.level}}
    div
      div
        span 經驗值
        span {{planet.experience}} / {{planetLevelInfo[planet.level].experience}}
      div
        span 星球規模
        span {{planet.scale}}
</template>

<script>
import planetLevelInfo from '@/assets/data/planet-level-info.json'
export default {
  props: {
    user: Object,
    planet: Object
  },
  data: function () {
    return {
      planetLevelInfo: planetLevelInfo.result
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
</style>
