<template lang="pug">
  div(class = "list-content")
    div(class = "sound-title")
      h1 音樂音量
    div(class= "sound-content")
      div(class="volume")
        img(v-bind:src="volumeIcon")
        h1 音樂
        div
          div(class = "volume-circle")
          hr(class = "volume-line")
      div(class="sound-effect")
        img(v-bind:src="soundIcon")
        h1 音效
        div
          div(class = "sound-circle")
          hr(class = "sound-line")
    div(class="account-title")
      h1 帳號設定
    div(class="account-content")
      img(v-bind:src="userIcon")
      h1 {{username}}
      h2 登出
</template>
<script>
export default {
  name: 'Setting',
  data: function () {
    return {
      musicVolume: 50,
      soundEffect: 50,
      username: 'TripleTien',
      volumeIcon: require('@/assets/icon/build.png'),
      soundIcon: require('@/assets/icon/build.png'),
      userIcon: require('@/assets/icon/build.png')
    }
  }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.list-content {
  display: grid;
  grid-template-rows: 19px 32px 95px 32px 34px;
  grid-template-columns: 260px;
  justify-content: center;
  grid-template-areas: "."
  "sound-title"
  "sound-content"
  "account-title"
  "account-content";
  .sound-title {
    grid-area: sound-title;
    >h1 {
      background-color: map-get($colors, primary);
      float: left;
      font-size: 18px;
      padding: 5px;
    }
  }
  .sound-content {
    grid-area: sound-content;
    display: grid;
    grid-template-rows: 20px 16px 18px 16px 25px;
    grid-template-areas: "."
    "volume"
    "."
    "sound-effect"
    ".";
    .volume {
    grid-area: volume;
    display: grid;
    grid-template-columns: 9px 10px 36px 14px 186px;
    grid-template-areas:
    "volumeImg . volumeTitle . volumeBar";
      >img {
        grid-area: volumeImg;
        width: 9px;
      }
      >h1 {
        grid-area: volumeTitle;
        font-size: 16px;
      }
      >div {
        grid-area: volumeBar;
        .volume-circle {
          z-index: 1;
          position: absolute;
          width: 17px;
          height: 17px;
          border-radius: 100%;
          background-color:white;
        }
        .volume-line {
          z-index: -1;
          position: absolute;
          width: 170px;
          height: 2px;
        }
      }
    }
    .sound-effect {
      grid-area: sound-effect;
      display: grid;
      grid-template-columns: 9px 10px 36px 14px 186px;
      grid-template-areas:
      "soundImg . soundTitle . soundBar";
      >img {
        grid-area: soundImg;
        width: 9px;
      }
      >h1 {
        grid-area: soundTitle;
        font-size: 16px;
      }
      >div {
        grid-area: soundBar;
        .sound-circle {
          z-index: 1;
          position: absolute;
          width: 17px;
          height: 17px;
          border-radius: 100%;
          background-color:white;
        }
        .sound-line {
          z-index: -1;
          position: absolute;
          width: 170px;
          height: 2px;
        }
      }
    }
  }
  .account-title {
    grid-area: account-title;
    >h1 {
      background-color: map-get($colors, primary);
      float: left;
      font-size: 18px;
      padding: 5px;
    }
  }
  .account-content {
    grid-area: account-content;
    display: grid;
    grid-template-columns: 9px 10px 78px 128px 30px;
    grid-template-rows: 17px 18px;
    grid-template-areas:
    ". . . . ."
    "icon . name . logout";
    >img {
      grid-area: icon;
      width: 9px;
    }
    >h1 {
      grid-area: name;
      font-size :18px;
    }
    >h2 {
      grid-area: logout;
      font-size: 12px;
      color: map-get($colors, white);
    }
  }
}
</style>
