<template lang="pug">
  div(class="collectingLayer" @click="$emit('close', '採集報告')")
    div
      video(autoplay playsinline muted)
        source(src="@/assets/video/collect/collecting.webm" type="video/webm")
    div
      div(ref="collectingReport")
        div(class="heading-default")
          h3 採集報告
        div
          div(class="collection-resource")
            div(v-for="resource in resources")
              i(:style="{'background-image': `url(${transformImage.things(resource.type)})`}")
              span {{resource.amount}}
</template>

<script>
import transformImage from '@/assets/javascript/transformImage.js'
export default {
  data () {
    return {
      transformImage: transformImage,
      resources: [
        {
          'type': '木頭',
          'amount': 100
        },
        {
          'type': '石頭',
          'amount': 80
        },
        {
          'type': '鋼鐵',
          'amount': 50
        },
        {
          'type': '銅礦',
          'amount': 20
        }
      ]
    }
  },
  mounted () {
    setTimeout(() => {
      this.$refs.collectingReport.style.opacity = 0.9
    }, 3000)
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.collectingLayer {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  > div:nth-child(2) {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    > div {
      box-sizing: border-box;
      width: 260px;
      height: 130px;
      background: map-get($colors, black);
      border-radius: 7.5px;
      border: map-get($colors, white) 1px solid;
      box-shadow: map-get($shadow, secondary-4);
      opacity: 0;
      transition: opacity 0.3s;
      padding: 14px 16px;
      display: grid;
      grid-template-rows: 25px 1fr;
      align-items: center;
      div:nth-child(2) {
        > .collection-resource {
          display: grid;
          grid-template-columns: 69px 69px;
          grid-template-rows: 18.5px 18.5px;
          grid-gap: 13.5px 31px;
          justify-content: center;
          > div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            > i {
              width: 22px;
              height: 18px;
              background-size: contain;
            }
            > span {
              font-size: 11px;
              line-height: 11px;
            }
          }
        }
      }
    }
  }
}
</style>
