<template lang="pug">
  div(class="user-panel")
    div(class="user-info")
      div(class="avatar" @click="openModal('user-profile')")
        div(class="portrait")
      div(class="name-group")
        div(class="username")
          p {{user.username}}
          label(class="nickname") {{user.nickname}}
        div(class="lv-info")
          div
            span {{planet.name}}
            span Level. {{planet.level}}
          button(@click="openModal('星球資訊')" class="show-info-btn")
    div(v-if="mode === 'Game'" class="wealth-info")
      div(class="charge-panel")
        i(:style="{'background-image':`url(${require('@/assets/icon/diamond.png')})`}")
        span {{user.diamond}}
        i
      div(class="charge-panel")
        i(:style="{'background-image':`url(${require('@/assets/icon/coin.png')})`}")
        span {{user.coin}}
        i
    div(v-if="mode === 'Game'" class="population-info")
      div
        i(:style="{'background-image':`url(${require('@/assets/icon/population.png')})`}")
        span {{planet.population.amount}}
      div
        i(:style="{'background-image':`url(${require('@/assets/icon/growth-rate.png')})`}")
        span {{planet.population.growth >= 0 ? '+' : `-`}}{{planet.population.growth}}/hr
    div(v-if="mode === 'Game'" class="button-group")
      i(class="flag" @click="backToOrig")
      router-link(tag="i" to="/home" class="back" @click.native="backToPlanetScene")
      i(class="scaling" @click="zooming")
</template>

<script>
import { Obj } from '@/game/scenes/PlayScene.js'

export default {
  props: {
    mode: String,
    user: Object,
    planet: Object
  },
  data () {
    return {
      module: null,
      module_2: null,
      isZoomIn: false
    }
  },
  beforeCreate () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
    import('@/game/scenes/HumanMainScene').then(async (module) => {
      this.module_2 = module
    })
  },
  methods: {
    openModal (one) {
      this.$emit('openModal', one)
    },
    backToOrig () {
      this.module.gameFunction.backToOrig()
    },
    backToPlanetScene () {
      if (Obj.nowScene === 'PlayScene') {
        this.module.gameFunction.changeScene('PlanetScene')
      }
    },
    zooming () {
      if (Obj.nowScene === 'PlayScene') {
        this.isZoomIn ? this.module.gameFunction.zoomInOut(1, 700, 'Quart.easeOut')
          : this.module.gameFunction.zoomInOut(0.5, 700, 'Quart.easeOut')
      } else if (Obj.nowScene === 'HumanMainScene') {
        this.isZoomIn ? this.module_2.gameFunction.zoomInOut(1, 700, 'Quart.easeOut')
          : this.module_2.gameFunction.zoomInOut(0.5, 700, 'Quart.easeOut')
      }
      this.isZoomIn = !this.isZoomIn
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.user-panel {
  .user-info {
    position: fixed;
    top: 31px;
    left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .avatar {
      position: absolute;
      box-sizing: border-box;
      width: 64px;
      height: 64px;
      background: linear-gradient(
        90deg,
        rgba(73, 103, 219, 0.8),
        rgba(170, 105, 181, 0.8)
      );
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      .portrait {
        width: 56px;
        height: 56px;
        background: white;
        background-image: url("~@/assets/icon/yu-portrait.png");
        background-position: center center;
        background-size: contain;
        background-repeat: no-repeat;
        border-radius: 50%;
      }
    }
    .name-group {
      .username {
        padding: 0px 0px 3px 73px;
        display: flex;
        > p:nth-child(1) {
          font-size: 16px;
          margin-right: 6px;
          text-shadow: map-get($shadow, secondary-8);
        }
        > label.nickname {
          align-self: center;
          font-size: 10px;
          padding: 0px 5px;
          display: block;
          border-radius: 4px;
          background-color: #4568dc;
          box-shadow: map-get($shadow, secondary-4);
        }
      }
      .lv-info {
        box-sizing: border-box;
        display: grid;
        grid-template-columns: 1fr 11px;
        grid-column-gap: 7px;
        align-items: center;
        width: 103px;
        background: map-get($linear-gradient, primary);
        box-shadow: map-get($shadow, secondary-4);
        border-radius: 6px;
        margin-left: 40px;
        padding: 0px 8px 0 20px;
        > div {
          display: flex;
          align-items: flex-end;
          flex-direction: column;
          > span {
            font-size: 10px;
          }
        }
        .show-info-btn {
          position: static;
          width: 11px;
          height: 11px;
        }
      }
    }
  }
  .wealth-info {
    position: fixed;
    top: 34px;
    right: 15px;
    > div:nth-child(1),
    > div:nth-child(2) {
      width: 90px;
      height: 16px;
      margin-bottom: 18px;
      > i:nth-child(1) {
        position: absolute;
        left: 0px;
        top: -17px;
        width: 30px;
        height: 35px;
        background-size: cover;
      }
      > i:nth-child(3) {
        position: absolute;
        right: 0;
        width: 16px;
        height: 16px;
      }
    }
  }
  .population-info {
    position: fixed;
    display: flex;
    top: 95px;
    left: 72px;
    > div {
      display: grid;
      grid-template-columns: 8px 32px;
      grid-column-gap: 5px;
      i {
        width: 8px;
        height: 10px;
        background-size: cover;
      }
      span {
        justify-self: start;
        font-size: 9px;
      }
    }
  }
  .button-group {
    display: flex;
    position: fixed;
    top: 113px;
    left: 39px;
    > i {
      width: 24px;
      height: 24px;
      border-radius: 50%;
      box-shadow: map-get($shadow, secondary-4);
      background-color: #fcfcfc;
    }
    > i:nth-child(1) {
      background-image: url("~@/assets/icon/position-flag.png");
      margin-right: 10px;
    }
    > i:nth-child(2) {
      background-image: url("~@/assets/icon/back.png");
      margin-right: 10px;
    }
    > i:nth-child(3) {
      background-image: url("~@/assets/icon/eye.png");
    }
  }
}
</style>
