import Vue from 'vue'
import Vuex from 'vuex'

import userModule from './modules/user.js'
import planetModule from './modules/planet.js'
import planetList from './modules/planetList.js'
import map from './modules/map.js'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    user: userModule,
    planet: planetModule,
    planetList: planetList,
    map: map
  }
})
