<template lang="pug">
  transition-group(tag="div" :name="transitionName" class="custom-transition-root")
    div(v-if="displayTransition" :key="'div'")
      slot(name="transitionContent")
</template>

<script>

export default {
  props: {
    transitionName: String
  },
  data () {
    return {
      displayTransition: false
    }
  },
  methods: {
    sleep (time) {
      return new Promise(resolve => setTimeout(resolve, time))
    }
  },
  async mounted () {
    this.displayTransition = true
    await this.sleep(3000)
    this.$emit('transitionEnd')
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.custom-transition-root {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 500;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba($color: #000000, $alpha: 0.7);
}

// 研發建築解鎖
.fade-down-up-enter-active {
  transition: all 0.8s 0.2s;
}
.fade-down-up-leave-active {
  transition: all 0.5s;
}
.fade-down-up-enter,
.fade-down-up-leave-to {
  transform: translateY(30px);
  opacity: 0;
}

// 升等
.fade-enter-active {
  transition: all 1s 0.3s;
}
.fade-leave-active {
  transition: all 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
