<template lang="pug">
  div(id="app")
    div(id="game-container")
    router-view
    level-up(v-if="isLevelUp" @close="close")
    loading(v-if="isLoading")
</template>

<script>
import LevelUp from '@/components/LevelUp.vue'
import Loading from '@/components/Loading.vue'
import { mapGetters } from 'vuex'

export default {
  components: {
    LevelUp,
    Loading
  },
  data () {
    return {
      isLevelUp: false,
      isFirstTime: true
    }
  },
  beforeMount () {
    import('@/game/game').then(game => {
      this.$nextTick(() => game.launch())
    })
  },
  mounted () {
    this.$store.watch((state, getters) => {
      return state.planet.planet.level
    }, (newValue, oldValue) => {
      if (this.isFirstTime) {
        this.isFirstTime = false
        console.log('first')
        return
      }
      if (newValue !== oldValue) {
        console.log('level-up')
        this.isLevelUp = true
      }
    }, { deep: true }
    )
  },
  computed: {
    ...mapGetters('user', {
      isLoading: 'getIsLoading'
    })
  },
  methods: {
    close () {
      this.isLevelUp = false
    }
  }

}
</script>

<style lang="scss">
@import "@/assets/scss/main.scss";
@import "@/assets/scss/variables.scss";

#app {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: map-get($colors, white);
}
#game-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
</style>
