import Vue from 'vue'
import store from '@/store'
import Router from 'vue-router'
import Game from './views/Game.vue'
import Collect from './views/Collect.vue'
import { PlanetGameFunc } from '@/game/scenes/PlanetScene'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'StartAnim',
      component: () => import('@/views/StartAnim.vue')
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('@/views/Login.vue')
    },
    {
      path: '/register',
      name: 'Register',
      component: () => import('@/views/Register.vue')
    },
    {
      path: '/home',
      name: 'Home',
      component: () => import('./views/Home.vue'),
      beforeEnter: async function (to, from, next) {
        try {
          store.commit('user/SET_ISLOADING', true)
          await store.dispatch('user/getUserData')
          if (store.getters['user/getIsInBeginningStory']) {
            next({ path: '/beginning_story' })
            return
          }
          await store.dispatch('planet/getPlanetData', { pid: store.getters['user/getCurrentPlanetId'], token: store.getters['user/getToken'] })
          store.commit('user/SET_ISLOADING', false)
          next()
        } catch (error) {
          console.log(error)
          next({ path: '/login' })
        }
      }
    },
    {
      path: '/game',
      name: 'Game',
      component: Game,
      beforeEnter: async function (to, from, next) {
        try {
          store.commit('user/SET_ISLOADING', true)
          await store.dispatch('user/getUserData')
          await store.dispatch('planet/getPlanetData', { pid: store.getters['user/getCurrentPlanetId'], token: store.getters['user/getToken'] })
          import('@/game/scenes/PlayScene')
            .then(module => module.getBuildingMap(store.getters['planet/getScale'], store.getters['planet/getBuildingMap']))
            .then(() => PlanetGameFunc.ChangeScene())
          if (store.getters['user/getIsBeginner']) {
            next({ path: '/beginner_guide' })
            return
          }
          store.commit('user/SET_ISLOADING', false)
          next()
        } catch (error) {
          console.log(error)
          next({ path: '/login' })
        }
      }
    },
    {
      path: '/beginning_story',
      name: 'BeginningStory',
      component: () => import('./views/BeginningStory.vue'),
      beforeEnter: function (to, from, next) {
        store.commit('user/SET_ISLOADING', false)
        next()
      }
    },
    {
      path: '/beginner_guide',
      name: 'BeginnerGuide',
      component: () => import('./views/BeginnerGuide.vue'),
      beforeEnter: function (to, from, next) {
        store.commit('user/SET_ISLOADING', false)
        next()
      }
    },
    {
      path: '/collect',
      name: 'Collect',
      component: Collect,
      beforeEnter: async function (to, from, next) {
        try {
          // await store.dispatch('user/getUserData')
          await store.dispatch('map/getMapGridData')
          next()
        } catch (error) {
          console.log(error)
        }
      },
      afterEach: function (to, from, next) {
        console.log('leave collect')
      }
    }
  ]
})
