<template lang="pug">
  div(class="modal-card-diamond-root")
    i(:style="{'background-image':`url(${require('@/assets/icon/diamond.png')})`}")
    div(v-if="!isInsufficient")
      p(v-html="displayedMsg")
      div
        button(@click="$emit('cancel')") 取消
        button(@click="confirm") 確認
    div(v-else)
      p(v-html="displayedMsg")
      div
        button(@click="$emit('cancel')") 返回
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  props: {
    msg: String,
    errorMsg: String,
    requiredDiamond: Number
  },
  data () {
    return {
      displayedMsg: '',
      isInsufficient: false
    }
  },
  created () {
    this.displayedMsg = this.msg.replace(/requiredDiamond/i, this.requiredDiamond)
  },
  computed: {
    ...mapGetters('user', {
      diamond: 'getDiamond'
    })
  },
  methods: {
    ...mapActions('user', ['spendDiamond']),
    async confirm () {
      try {
        await this.spendDiamond({ diamond: this.requiredDiamond })
        this.$emit('confirm')
      } catch (error) {
        this.isInsufficient = true
        this.displayedMsg = this.errorMsg.replace(/requiredDiamond/i, this.requiredDiamond)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.modal-card-diamond-root {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 85px 170px;
  grid-template-rows: 64px;
  width: 283px;
  height: 92px;
  padding: 13px;
  border: 1px solid map-get($colors, white);
  box-shadow: map-get($shadow, secondary-4);
  background-color: rgba($color: map-get($colors, black), $alpha: 0.7);
  border-radius: 7.5px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);

  > i {
    background-size: cover;
    width: 100px;
    height: 90px;
    position: relative;
    top: -35px;
    left: -15px;
  }
  > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    > p {
      height: 34px;
      font-size: 14px;
      line-height: 17px;
      font-weight: 900;
      text-align: left;
    }
    > div {
      display: flex;
      justify-content: flex-end;
      > button {
        font-size: 14px;
        font-weight: 900;
      }
      > button:nth-child(2) {
        color: #ff417c;
        margin-left: 11px;
      }
    }
  }
}
</style>
