<template lang="pug">
  div(id="game-root")
    user-panel(
      v-if="!isOnBuildMode"
      :user="user"
      :planet="planet"
      :mode="'Game'"
      @openModal="openModal")
    feature-menu(v-if="!isOnBuildMode && !isSelectBuilding" class="feature-menu" @openModal="openModal" :isModalOpen="!isModalOpen")
    building-info(v-else-if="isSelectBuilding" :building="selectedBuilding" @openModal="openModal" @close="close")
    transition(name="modal")
      modal-card(v-if="isModalOpen" @close="close")
        user-profile(v-if="currentOpen === 'user-profile'" slot="card" :user="user")
        planet-info(v-if="currentOpen === '星球資訊'" slot="card" :user="user" :planet="planet")
        building-upgrade(v-if="currentOpen === '建築升級'" slot="card" :building="selectedBuilding")
        building-feature(v-if="currentOpen === '建築功能'" slot="card" :user="user" :building="selectedBuilding" :planet="planet")
        building(v-if="currentOpen === '建造頁面'" slot="card" :user="user" :planet="planet" @onBuildMode="onBuildMode")
        warehouse(v-if="currentOpen === '資源頁面'" slot="card" :user="user")
        mission(v-if="currentOpen === '任務頁面'" slot="card" :user="user" :planet='planet')
    build-mode(v-if="isOnBuildMode" @close="close" :building="selectedBuilding")
</template>

<script>
import FeatureMenu from '@/components/game/FeatureMenu.vue'
import Building from '@/components/game/build/Building.vue'
import BuildingFeature from '@/components/game/build/buildingFeature/BuildingFeature.vue'
import BuildingInfo from '@/components/game/build/BuildingInfo.vue'
import BuildingUpgrade from '@/components/game/build/BuildingUpgrade'
import BuildMode from '@/components/game/build/BuildMode.vue'
import Mission from '@/components/game/mission/Mission.vue'
import ModalCard from '@/components/ModalCard.vue'
import PlanetInfo from '@/components/game/planetInfo/PlanetInfo.vue'
import UserPanel from '@/components/user/UserPanel.vue'
import UserProfile from '@/components/user/UserProfile.vue'
import Warehouse from '@/components/game/warehouse/Warehouse.vue'
import { Obj } from '@/game/scenes/PlayScene.js'

import { mapState } from 'vuex'

export default {
  name: 'Game',
  components: {
    FeatureMenu,
    Building,
    BuildingFeature,
    BuildingInfo,
    BuildingUpgrade,
    BuildMode,
    Mission,
    ModalCard,
    PlanetInfo,
    UserPanel,
    UserProfile,
    Warehouse
  },
  data () {
    return {
      isModalOpen: false,
      currentOpen: '',
      downloaded: false,
      isOnBuildMode: false,
      module: null,
      module_2: null,
      isSelectBuilding: false,
      selectedBuilding: ''
    }
  },
  mounted () {
    this.$store.watch((state, getters) => {
      return getters
    }, (newValue, oldValue) => {
      this.isSelectBuilding = newValue['planet/getIsSelectBuilding']
      this.selectedBuilding = newValue['planet/getBuildElement']
    }, { deep: true }
    )
  },
  created () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
    import('@/game/scenes/HumanMainScene').then(async (module) => {
      this.module_2 = module
    })
    this.$bus.$on('openModal', this.openModal)
    this.$bus.$on('close', this.close)
  },
  beforeDestroy () {
    this.$bus.$off('openModal', this.openModal)
    this.$bus.$off('close', this.close)
  },
  methods: {
    openModal (one) {
      this.isModalOpen = true
      this.isOnBuildMode = false
      this.currentOpen = one
    },
    close () {
      this.isModalOpen = false
      this.isOnBuildMode = false
      this.module.gameFunction.setIsOnBuildMode(false)
      this.$store.commit('planet/SET_ISSELECTBUILDING', false)
    },
    onBuildMode (building) {
      this.isModalOpen = false
      this.isOnBuildMode = true
      this.selectedBuilding = building
    }
  },
  watch: {
    isModalOpen: function () {
      if (Obj.nowScene === 'PlayScene') {
        this.module.gameFunction.scene.input.enabled = !this.isModalOpen
      } else if (Obj.nowScene === 'HumanMainScene') {
        this.module_2.gameFunction.scene.input.enabled = !this.isModalOpen
      }
    }
  },
  computed: {
    ...mapState('user', {
      user: state => state
    }),
    ...mapState('planet', {
      planet: state => state.planet
    })
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

#game-root {
  #game-container {
    height: 100%;
    overflow-y: hidden;
  }
  .feature-menu {
    position: fixed;
    bottom: 33px;
  }
}
.modal-enter-active {
  transition: all 1s;
}
.modal-leave-active {
  transition: all 0.5s;
}
.modal-enter,
.modal-leave-to {
  opacity: 0;
}
</style>
