<template lang="pug">
  div(class="user-profile-root")
    div(id="head")
      div
        img(src="@/assets/icon/yu-portrait.png")
      div
        h3 {{user.username}}
        h6 稱號：{{user.nickname}}
    tab(
      :navList="navList"
      @switchMenu="switchMenu")
      div(slot="content")
        planet-list(v-if="currentMenu === '星球列表'" :planetList="user.planetList.slice(1)")
        achievement(v-if="currentMenu === '成就稱號'" :achievements="user.achievements")
        illustration(v-if="currentMenu === '建築圖鑑'")
        setting(v-if="currentMenu === '遊戲設定'")
</template>

<script>
import Tab from '@/components/Tab.vue'
import Achievement from '@/components/user/Achievement.vue'
import PlanetList from '@/components/user/PlanetList.vue'
import Illustration from '@/components/user/Illustration.vue'
import Setting from '@/components/user/Setting.vue'
import { mapActions } from 'vuex'
export default {
  components: {
    Tab,
    Achievement,
    PlanetList,
    Illustration,
    Setting
  },
  name: 'Profile',
  props: {
    user: Object
  },
  data: function () {
    return {
      navList: ['星球列表', '成就稱號', '建築圖鑑', '遊戲設定'],
      currentMenu: '星球列表'
    }
  },
  async created () {
    await this.getPlanetList()
  },
  methods: {
    ...mapActions('user', ['getPlanetList']),
    switchMenu (one) {
      this.currentMenu = one
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.user-profile-root {
  display: grid;
  grid-template-rows: 88px 20px 1fr;
  grid-template-areas:
    "avatar"
    "."
    "nav";
  #head {
    grid-area: avatar;
    display: flex;
    padding: 6px;
    > div:nth-child(1) {
      img {
        width: 68px;
        height: 68px;
        background-size: contain;
        border-radius: 50%;
        border: 4px solid map-get($colors, primary);
        box-shadow: 0 0 4px map-get($colors, primary);
      }
    }

    > div:nth-child(2) {
      padding-left: 23px;
      text-align: left;
      display: flex;
      flex-direction: column;
      justify-content: center;

      > h3 {
        font-size: 18px;
        line-height: 29px;
      }

      > h6 {
        font-size: 12px;
      }
    }
  }
  > div:nth-child(2) {
    grid-area: nav;
    grid-row-gap: 14px;
    /deep/ .nav {
      span {
        font-size: 11px;
        line-height: 11px;
      }
    }
    > div:nth-child(2) {
      height: 331px;
      overflow-y: scroll;
    }
  }
}
</style>
