<template lang="pug">
  div(class="architecture-technology-deatail-root")
    div
      div(class="image-container")
        div(:class="{unlocked: !archiTech.isLocked}")
          img(:src="archiTech.isLocked ? transformImage.archiTechLocked(archiTech.name) : transformImage.archiTech(archiTech.name)")
      div
        h4 {{archiTech.title}}
        span(v-if="archiTech.isLocked") 研發所需點數 : {{archiTech.point}}
        span(v-else) 研發所需點數 : {{archiTech.nextLevel === 'none' ? '無' : archiTech.point}}
        span(v-if="archiTech.isLocked") 目前研發狀態：未解鎖
        span(v-else) 目前研發狀態： Lv.{{archiTech.nextLevel === 'none' ? 'MAX' : archiTech.level}}
    div
      div(class="heading-default")
        h4 研發影響
      div(class="content")
        p {{archiTech.description}}
        div
          img(:src="transformImage.building(archiTech.name)")
    div
      span(@click="back") 返回上頁
      span(v-if="archiTech.isLocked" @click="devArchiTech(archiTech)") 進行研發
    error-message(v-if="isError" :errorMsg="errorMsg")
</template>

<script>
import transformImage from '@/assets/javascript/transformImage.js'
import ErrorMessage from '@/components/ErrorMessage.vue'
import { mapGetters } from 'vuex'

export default {
  components: {
    ErrorMessage
  },
  props: {
    archiTech: Object
  },
  data () {
    return {
      transformImage: transformImage,
      errorMsg: '',
      isError: false
    }
  },
  computed: {
    ...mapGetters('planet', {
      architectureTechnologyPoint: 'getArchitectureTechnologyPoint'
    })
  },
  methods: {
    showIamge (isLocked) {
      if (isLocked) {
        return require('@/assets/image/architectureTechnology/book-inactive.png')
      } else {
        return require('@/assets/image/architectureTechnology/book-active.png')
      }
    },
    back () {
      this.$emit('back')
    },
    async devArchiTech (archiTech) {
      if (archiTech.point > this.architectureTechnologyPoint) {
        this.displayError('技能點數不足！')
        return
      }
      this.$emit('nextScenes', '進行研發')
      this.$emit('develop')
    },
    displayError (msg) {
      let self = this
      self.errorMsg = msg
      self.isError = true
      setTimeout(() => {
        self.isError = false
      }, 2000)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.architecture-technology-deatail-root {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 95px 15px 254px 26px 16px;
  grid-template-areas:
    "title"
    "."
    "main"
    "."
    "button-group";
  > div:nth-child(1) {
    grid-area: title;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 95px 1fr;
    grid-template-rows: auto;
    grid-column-gap: 25px;
    align-items: center;
    > .image-container {
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 95px;
      height: 95px;
      background-color: #242236;
      border-radius: 50%;
      > div {
        width: 85px;
        height: 85px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          width: 45px;
          // height: 36px;
        }
      }
      .unlocked {
        border: 1px solid map-get($colors, secondary);
      }
    }
    > div:nth-child(2) {
      display: grid;
      grid-template-rows: auto auto auto;
      grid-row-gap: 12px;
      justify-items: start;
      h4 {
        font-size: 16px;
        line-height: 16px;
        text-shadow: map-get($shadow, secondary-4);
      }
      span {
        font-size: 12px;
        line-height: 12px;
      }
    }
  }
  > div:nth-child(2) {
    grid-area: main;
    .content {
      > p {
        height: 71px;
        padding: 14px 0 18px 0;
        font-size: 11px;
        line-height: 18px;
      }
      > div {
        height: 128px;
        > img {
          height: 128px;
        }
      }
    }
  }
  > div:nth-child(3) {
    grid-area: button-group;
    display: flex;
    justify-content: center;
    > span {
      font-size: 16px;
      line-height: 16px;
      font-weight: 900;
    }
    > span:nth-child(2) {
      color: #ff417c;
      padding-left: 24px;
    }
  }
}
</style>
