<template lang="pug">
  div(class="general-mission-root")
    div(class="mission-container" v-for="mission in generalMissions")
      div
        h3 {{mission.name}}
        div(v-if="mission.isCompleted")
          span 領取
        div(v-else class="undone")
          span 未完成
      div
        div
          span {{mission.description}}
          span ( {{mission.currentValue}} / {{mission.goal}} )
        div
          div(v-bind:style="{width: progressBar(mission.currentValue, mission.goal) + '%'}")
      div
        span 完成獎勵
      div
        div(v-for="resource in mission.prize.resources")
          i(:style="{'background-image': `url(${transformImage.things(resource.type)})`}")
          span {{resource.amount}}
</template>

<script>
import missions from '@/assets/data/mission.json'
import transformImage from '@/assets/javascript/transformImage.js'

export default {
  props: {
    user: Object,
    planet: Object
  },
  data () {
    return {
      transformImage: transformImage,
      generalMissions: missions.generalMissions
    }
  },
  created () {
    this.generalMissions.forEach(mission => {
      mission.isCompleted = false
      switch (mission.type) {
        case 'building':
          mission.currentValue = this.planet.buildingMap.length
          break
        case 'resource':
          mission.currentValue = this.user.resources.map(resource => resource.amount).reduce((total, amount) => total + amount)
          break
      }
      if (this.progressBar(mission.currentValue, mission.goal === 100)) {
        mission.isCompleted = true
      }
    })
  },
  methods: {
    progressBar: function (currentValue, goal) {
      return 100 * (currentValue / goal) >= 100 ? 100 : 100 * (currentValue / goal)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.general-mission-root {
  display: grid;
  grid-auto-rows: 135px;
  grid-row-gap: 15px;
  flex-direction: column;
  align-items: center;
  h3 {
    font-size: 18px;
    line-height: 18px;
    text-shadow: map-get($shadow, secondary-4);
  }
  span {
    font-size: 10px;
    line-height: 10px;
    text-shadow: map-get($shadow, secondary-4);
  }
  > .mission-container {
    box-sizing: border-box;
    display: grid;
    grid-template-rows: 18px 14px 18px 13px 13px 9px 20px;
    grid-template-areas:
      "head"
      "."
      "progress-bar"
      "."
      "finish"
      "."
      "prize";
    border-radius: 4px;
    border: 1px solid map-get($colors, primary);
    box-shadow: map-get($shadow, secondary-4);
    padding: 14px 15px;
    > div:nth-child(1) {
      grid-area: head;
      display: flex;
      justify-content: space-between;
      align-items: center;

      > div {
        display: flex;
        justify-content: center;
        align-items: center;
        background: map-get($linear-gradient, primary);
        border-radius: 2.5px;
        height: 18px;
        width: 43px;
      }
      .undone {
        background: transparent;
        border: 1px solid map-get($colors, primary);
      }
    }
    > div:nth-child(2) {
      grid-area: progress-bar;
      > div:nth-child(1) {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      > div:nth-child(2) {
        background-color: map-get($colors, white);
        width: 100%;
        height: 4px;
        margin-top: 4px;
        > div {
          background: map-get($linear-gradient, primary);
          height: 4px;
        }
      }
    }
    > div:nth-child(3) {
      grid-area: finish;
      text-align: start;
      border-bottom: 1px solid map-get($colors, primary);
      > span {
        display: block;
        color: map-get($colors, primary);
        text-shadow: map-get($shadow, secondary-4);
        padding-bottom: 2px;
      }
    }
    > div:nth-child(4) {
      grid-area: prize;
      display: flex;
      justify-content: space-between;
      div {
        display: flex;
        align-items: center;
        > i {
          width: 23px;
          height: 20px;
          background-size: contain;
          margin-right: 7px;
        }
      }
    }
  }
}
</style>
