<template lang="pug">
  div(class="building-info")
    template(v-if="building.status === 'done'")
      div
        h4 {{building.name_zh}}
        button(@click.stop="removeBuilding") 拆除
      div
        button(class="icon-button")
        button(@click.stop="openModal('建築升級')" class="icon-button")
        button(@click.self="openModal('建築功能')" class="icon-button")
    template(v-else-if="building.status === 'ing'")
      div
        h4 {{building.name_zh}}
        button(@click.stop="isbuildImmediately = true") 立即完成
      div
        span 剩餘建造時間 {{remainingTime}}
    modal-card-diamond(v-if="isbuildImmediately"
      :msg="'請問是否花費 requiredDiamond克金石，<br/>立即完成建造?'"
      :errorMsg="'剩餘不足 requiredDiamond克金石，<br/>無法立即建造完成。'"
      :requiredDiamond="12"
      @cancel="isbuildImmediately = false"
      @confirm="buildImmediately")
</template>

<script>
import ModalCardDiamond from '@/components/ModalCardDiamond.vue'
import { mapGetters } from 'vuex'

export default {
  components: {
    ModalCardDiamond
  },
  props: {
    building: Object
  },
  data () {
    return {
      module: null,
      remainingTime: '',
      isbuildImmediately: false,
      timer: null
    }
  },
  beforeCreate () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
  },
  beforeMount () {
    clearInterval(this.timer)
    if (this.building.status === 'ing') {
      this.countdown()
      this.timer = setInterval(this.countdown, 1000)
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
  },
  computed: {
    ...mapGetters('user', {
      isBeginner: 'getIsBeginner',
      currentScene: 'getCurrentBeginnerGuideScenes'
    })
  },
  methods: {
    openModal (one) {
      if (this.isBeginner) {
        if (this.currentScene === 94 && this.building.name_zh !== '黑帝斯聖殿') { return }
      }
      this.$emit('openModal', one)
    },
    close () {
      this.module.gameFunction.scene.input.enabled = true
      this.$emit('close')
    },
    async buildImmediately () {
      try {
        this.isbuildImmediately = false
        this.module.gameFunction.directlyBuild(this.building)
        this.close()
      } catch (error) {
        console.log(error)
      }
    },
    async removeBuilding () {
      if (this.isBeginner) {
        return
      }
      await this.module.gameFunction.removeBuilding(this.building)
      await this.$store.dispatch('planet/removeBuilding', { building: this.building })
      this.close()
    },
    countdown: function () {
      if (this.building.status === 'ing') {
        const end = Date.parse(new Date(this.building.finishTime))
        const now = Date.parse(new Date())
        const msec = end - now
        let day = parseInt(msec / 1000 / 60 / 60 / 24)
        let hr = String(parseInt(msec / 1000 / 60 / 60 % 24)).padStart(2, '0')
        let min = String(parseInt(msec / 1000 / 60 % 60)).padStart(2, '0')
        let sec = String(parseInt(msec / 1000 % 60)).padStart(2, '0')
        this.remainingTime = day > 0 ? `${day}天` : ''
        this.remainingTime += `${hr}:${min}:${sec}`
        if (msec <= 0) {
          clearInterval(this.timer)
          this.close()
        }
      }
    }
  },
  watch: {
    isbuildImmediately: function () {
      this.module.gameFunction.scene.input.enabled = !this.isbuildImmediately
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.building-info {
  box-sizing: border-box;
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 83px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 22px;
  background: linear-gradient(
    0deg,
    rgba(27, 18, 41, 1) 0%,
    rgba(24, 16, 36, 0.89) 25%,
    rgba(15, 10, 23, 0.58) 50%,
    rgba(1, 1, 2, 0.06) 75%
  );
  > div:nth-child(1) {
    display: flex;
    align-items: center;
    > h4 {
      font-size: 20px;
      text-shadow: map-get($shadow, primary-4);
      margin-right: 9px;
    }
    > button {
      padding: 4px 5px;
      border: 1px solid #ff417c;
      border-radius: 2px;
      background-color: map-get($colors, black);
      color: #ff417c;
      box-shadow: map-get($shadow, secondary-4);
      font-size: 9px;
      line-height: 9px;
    }
  }
  > div:nth-child(2) {
    display: flex;
    align-content: center;
    > button {
      width: 29px;
      height: 29px;
      &::after {
        position: relative;
        top: 27px;
        text-shadow: map-get($shadow, primary-4);
        font-size: 10px;
      }

      &:nth-child(1) {
        background-image: url("~@/assets/icon/move.png");
        margin-right: 11px;
        &::after {
          content: "搬移";
        }
      }
      &:nth-child(2) {
        background-image: url("~@/assets/icon/building-upgrade.png");
        margin-right: 11px;
        &::after {
          content: "資訊";
        }
      }
      &:nth-child(3) {
        background-image: url("~@/assets/icon/building-function.png");
        &::after {
          content: "功能";
        }
      }
    }
    > span {
      font-size: 14px;
      line-height: 14px;
    }
  }
}
</style>
