<template lang="pug">
  div(id="list-content")
    div(v-for="achievement in achievements")
      div
        h3 {{achievement.title}}
        span {{achievement.brief}}
      div
        div(v-if="achievement.state" :class="{'use': achievement.state}")
          span 使用中
        div(v-else @click="switchAchievement(achievement.title)")
          span 變更
</template>

<script>
export default {
  props: {
    achievements: Array
  },
  data: function () {
    return {
    }
  },
  methods: {
    switchAchievement (title) {
      this.achievements.forEach(achievement => {
        if (achievement.title === title) {
          achievement.state = true
        } else {
          achievement.state = false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

#list-content {
  display: grid;
  grid-template-columns: 267px;
  grid-auto-rows: 54px;
  grid-row-gap: 10px;
  justify-content: center;
  > div {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    border: 1px solid map-get($colors, primary);
    box-shadow: 0 0 2px map-get($colors, primary);
    padding: 11px 12px;
    > div:nth-child(1) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      > h3 {
        font-size: 18px;
        line-height: 18px;
        text-shadow: map-get($shadow, secondary-4);
      }
      > span {
        color: rgba(237, 242, 244, 0.6);
        font-size: 10px;
        opacity: 0.6;
        text-shadow: map-get($shadow, secondary-4);
      }
    }

    > div:nth-child(2) {
      align-items: center;
      display: flex;
      text-align: center;
      div {
        border-radius: 3px;
        height: 18px;
        line-height: 14px;
        width: 43px;
        border: 1px solid map-get($colors, primary);
      }
      span {
        font-size: 10px;
      }
      .use {
        background: map-get($linear-gradient, primary);
        border: none;
      }
    }
  }
}
</style>
