<template lang="pug">
  tab(
    class="architecture-technology-center-root"
    :navList="navList"
    @switchMenu="switchMenu"
    :class=" !isSelect ? 'row-gap-45' : 'row-gap-31'")
      div(slot="content")
        skills-tree(
          v-if="!isSelect"
          @click-node="clickNode"
          :skillsInfo="buildings[currentMenu]"
          :planet="planet")
        architecture-technology-detail(
          v-if="isSelect"
          :archiTech="archiTech"
          @back="back"
          @develop="develop"
          @nextScenes="nextScenes")
        span(class="point") 科研點數 : {{planet.architectureTechnologyPoint}}
        custom-transition-group(v-if="haveDeveloped"
          :transitionName="'fade-down-up'"
          @transitionEnd="transitionEnd")
          div(slot="transitionContent")
            div
              img(:src="transformImage.building(archiTech.name)")
            span 已解鎖 {{archiTech.name}}
</template>

<script>
import Tab from '@/components/Tab.vue'
import SkillsTree from '@/components/game/build/buildingFeature/SkillsTree.vue'
import ArchitectureTechnologyDetail from '@/components/game/build/buildingFeature/ArchitectureTechnologyDetail.vue'
import CustomTransitionGroup from '@/components/CustomTransitionGroup.vue'
import archiTechTreeData from '@/assets/data/architecture-technology-tree.json'
import transformImage from '@/assets/javascript/transformImage.js'
import { mapGetters, mapActions } from 'vuex'
export default {
  components: {
    SkillsTree,
    Tab,
    ArchitectureTechnologyDetail,
    CustomTransitionGroup
  },
  props: {
    planet: Object
  },
  data () {
    return {
      transformImage: transformImage,
      navList: ['通用科研', '軍事科研', '經濟科研', '特色建築'],
      currentMenu: '通用科研',
      buildings: {
        '通用科研': archiTechTreeData.commonBuilding,
        '軍事科研': archiTechTreeData.militaryBuilding,
        '經濟科研': archiTechTreeData.economicBuilding,
        '特色建築': archiTechTreeData.specialBuilding
      },
      isSelect: false,
      archiTech: '',
      haveDeveloped: false
    }
  },
  computed: {
    ...mapGetters('user', {
      isBeginner: 'getIsBeginner',
      currentScene: 'getCurrentBeginnerGuideScenes'
    })
  },
  methods: {
    ...mapActions('planet', ['devArchitectureTechnology', 'gainExperience']),
    switchMenu (one) {
      this.isSelect = false
      this.currentMenu = one
      this.nextScenes('切換' + one)
      this.$forceUpdate()
    },
    clickNode: function (node) {
      if (this.isBeginner) {
        if (this.currentScene === 10) { return }
        if (this.currentScene === 11 && node.name !== '倉庫') { return }
        if (this.currentScene === 12 && node.name !== '倉庫') { return }

        if (this.currentScene === 31) { return }
        if (this.currentScene === 32 && node.name !== '資源傳輸中心') { return }
        if (this.currentScene === 33 && node.name !== '資源傳輸中心') { return }

        if (this.currentScene === 60) { return }
        if (this.currentScene === 61 && node.name !== '住宅') { return }
        if (this.currentScene === 62 && node.name !== '住宅') { return }

        if (this.currentScene === 78) { return }
        if (this.currentScene === 79 && node.name !== '市政中心') { return }
        if (this.currentScene === 80 && node.name !== '市政中心') { return }
        this.nextScenes('研發' + node.name)
      }
      this.isSelect = true
      this.archiTech = node
    },
    back () {
      this.isSelect = false
    },
    async develop () {
      this.isSelect = false
      this.haveDeveloped = true
      await this.devArchitectureTechnology(this.archiTech)
    },
    nextScenes (action) {
      this.$bus.$emit('nextScenes', action)
    },
    transitionEnd () {
      this.haveDeveloped = false
      this.gainExperience(this.archiTech.experience)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.architecture-technology-center-root {
  position: relative;
  /deep/ .nav {
    span {
      font-size: 11px;
      line-height: 11px;
    }
  }
  .point {
    position: absolute;
    top: 39px;
    right: 0px;
    font-size: 10px;
    line-height: 10px;
  }
}
.row-gap-45 {
  grid-row-gap: 45px;
}
.row-gap-31 {
  grid-row-gap: 31px;
}
</style>
