<template lang="pug">
  tab(
    class="mission-root"
    :navList="navList"
    @switchMenu="switchMenu")
    div(slot="content")
      story-mission(v-if="currentMenu === '劇情任務'")
      general-mission(v-if="currentMenu === '一般任務'" :user="user" :planet="planet")

</template>

<script>
import Tab from '@/components/Tab.vue'
import StoryMission from '@/components/game/mission/StoryMission'
import GeneralMission from '@/components/game/mission/GeneralMission.vue'

export default {
  components: {
    Tab,
    StoryMission,
    GeneralMission
  },
  props: {
    user: Object,
    planet: Object
  },
  data () {
    return {
      navList: ['一般任務', '劇情任務'],
      currentMenu: '一般任務'
    }
  },
  methods: {
    switchMenu (one) {
      this.currentMenu = one
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.mission-root {
  /deep/ .nav {
    span {
      font-size: 13px;
      line-height: 13px;
    }
  }
  > div:nth-child(2) {
    display: grid;
    grid-template-rows: auto 1fr; // 多切一個空白內容，去吸收auto佔滿版的問題
    grid-template-areas:
      "content"
      ".";
    overflow-y: scroll;
    > div {
      grid-area: content;
    }
  }
}
</style>
