/* eslint-disable */
import buildingInfo from '@/assets/data/building-info.json'
import { w, h, Obj, userData, pointer } from '@/game/scenes/PlayScene.js'
import { PlanetGameFunc } from '@/game/scenes/PlanetScene'
import { TSConstructSignatureDeclaration, isModuleSpecifier, thisExpression } from 'babel-types';
import { UV_UDP_REUSEADDR } from 'constants';
import { buildGrid } from '@/components/game/build/Building.vue'
let isOnBuildMode = false
let isModalOpen = false

export default class GameFunction {
  constructor (scene) {
    this.scene = scene
  }

  async createBuilding (id, ele, spawn) {
    console.log('create')
    let self = this
    let x = ele.coordinateX
    let y = ele.coordinateY

    // 獲取建築資訊
    const DATA = await getData(id)
    let row = DATA.grid.row
    let column = DATA.grid.column
    let name = DATA.name
    let motion = DATA.motion
    
    let adjustPosition = positionAdjust(x, y, row, column)
    let newOne = this.scene.add.sprite(adjustPosition.x, adjustPosition.y, name+'ing').setOrigin(0.5, 1)
    // 將 userData 有關建築的數值給予 group element
    newOne.setInteractive(this.scene.input.makePixelPerfect())
    newOne.depth = newOne.y + 108
    newOne.name = name
    newOne.coordinateX = x
    newOne.coordinateY = y
    newOne.row = row
    newOne.column = column
    // if (row === 2 && column === 2 && newOne.coordinateX > 1) {
    //   newOne.depth = newOne.y + 54
    // }
    if (row === 2 && column === 2) {
      newOne.depth = newOne.y + 54
    }

    // 生成動畫
    if (spawn) { // 非一開始存在 點擊建造完成後主動生成
      var spawnAnimConfig = {
        key: name + x + y,
        frames: name,
        frameRate: 30,
        repeat: 0
      }
      this.scene.anims.create(spawnAnimConfig)
      if( motion.type === 'dynamic' ) {
        let thisSpirite  = newOne
        newOne.on('animationcomplete', function () {
          self.scene.anims.create({
            key: name + 'ing' + x + y,
            frames: name + 'ing',
            frameRate: motion.rate,
            repeat: -1,
            repeatDelay: motion.repeatDelay
          })
          console.log(newOne)
          thisSpirite.play(name+ 'ing' + x + y)
        }, this.scene)
      }
      newOne.play(name + x + y) // 播放生成動畫
    } else if (!spawn && motion.type === 'dynamic') { // 一開始即存在 開始播放動畫
      let yoyo
      id === 'b0' || id === 'b1' ? yoyo = true : yoyo = false
      this.scene.anims.create({
        key: name + 'ing' + x + y,
        frames: name + 'ing',
        frameRate: motion.rate,
        repeat: -1,
        yoyo: yoyo,
        repeatDelay: motion.repeatDelay
      })
      newOne.play(name + 'ing' + x + y)
    }

    // 設定時器，若建造完成則更換成completed型態
    if (ele.status === 'ing') {
      let remainTime = ele.finishTime - Date.now()
      newOne.timer = setTimeout(() => {
        console.log('要生成了')
        console.log(userData)
        userData.buildingMap.map((ele, index) => {
          if (ele.coordinateX === x && ele.coordinateY === y){
            self.scene.buildingGroup.getChildren().map(ele => {
              if (ele.coordinateX === x && ele.coordinateY === y) ele.destroy(true)
            })
            userData.buildingMap[index].status = 'completed'
            self.createBuilding('b1', ele)
          }
        })
      }, remainTime)
    }

    // 增加點擊事件
    newOne.on('pointerdown', function(pointer, x, y, event){
      if (!isOnBuildMode 
          && ((pointer.position.y > 140 && pointer.position.y < 576 )
          || (pointer.position.y < 140 && (pointer.position.x > 195 && pointer.position.x < 265) 
          || ((pointer.position.y > 92 && pointer.position.y < 140) && pointer.position.x > 265) ))) { // 在非建造模式才可以點擊
        if (this.isJump ) { return false }
        this.isJump = true
        if (Obj.lastTile) {
          // this.scene.tweens.add({
          //     targets: Obj.lastTile,
          //     duration: 130,
          //     y: '+=20',
          //     ease: 'Quad.easeOut',
          // })
          Obj.lastTile.isJump = false
          Obj.lastTile = null
        }
        // this.scene.tweens.add({
        //   targets: this,
        //   duration: 130,
        //   y: '-=20',
        //   ease: 'Quad.easeOut',
        // })
        Obj.lastTile = this
        // 取消所有色調，並加在本身建築物上
        this.scene.buildingGroup.getChildren().map( ele => {ele.clearTint() } )
        this.setTint(0xEDE3B7)
  
        // 點擊完成的建築，做完成動畫
        if(ele.status === 'completed'){
          // 修改 userdata
          ele.status = 'done'
          
          // 修改 Phaser中的 buildingGroup
          this.scene.buildingGroup.remove(this, true, true)

          self.createBuilding(ele.id, ele, true)

          // 設為false讓他可以回到原本地圖畫面
          import('../../store/modules/planet')
            .then(module => {
              module.default.actions.haveBuiltBuilding(module.default, { building: ele })
              return module
            })
            .then(module => {
              module.default.mutations.setBuildingElement(ele, false)
            })
        } else { // 如果是ing或者done，先設為false再改變buildingInfo
          import('../../store/modules/planet').then(module => {
            module.default.mutations.setBuildingElement(null, false)
            return module
          }).then((module) => {
            module.default.mutations.setBuildingElement(ele, true)
          })
        }
        event.stopPropagation() 
      }
    })
    // 將新的加到 buildingGroup
    this.scene.buildingGroup.add(newOne)
  }
  
  // 參數 座標 並判斷能不能蓋
  async checkIfCanBuild (x, y, id) {
    const DATA = await getData(id)
    let row = DATA.grid.row
    let column = DATA.grid.column
    
    if (x !== undefined && y !== undefined) {
      let occupiedArr = []
      // 判斷已經建造的地盤有哪些，存進 occupiedArr
      await this.scene.buildingGroup.getChildren().map(ele => {
        // 1x1處理
        occupiedArr.push({ x: ele.coordinateX, y: ele.coordinateY })
        // 非1x1需特殊處理
        if (ele.row === 2 && ele.column === 2) {
          occupiedArr.push(
            { x: ele.coordinateX-1, y: ele.coordinateY },
            { x: ele.coordinateX, y: ele.coordinateY-1 },
            { x: ele.coordinateX-1, y: ele.coordinateY-1 }
          )
        } else if (ele.row === 1 && ele.column === 2) {
          occupiedArr.push(
            { x: ele.coordinateX, y:ele.coordinateY-1 }
          )
        }
      })
      
      // 判斷目前選定的座標是否有在 occupiedArr
      let ifOccupied
      if( row === 1 && column === 1 ) {
        ifOccupied = occupiedArr.filter(ele => {
          return ele.x === x && ele.y === y
        }).length
      } else if ( row === 2 && column === 2 ) {
        ifOccupied = occupiedArr.filter(ele => {
          return (ele.x === x && ele.y === y) || 
            (ele.x === x-1 && ele.y === y) || 
            (ele.x === x && ele.y === y-1) || 
            (ele.x === x-1 && ele.y === y-1)
        }).length
        // 地圖邊緣
        if (x === 0 || y === 0) ifOccupied++
      } else if ( row === 1 && column === 2 ) {
        ifOccupied = occupiedArr.filter(ele => {
          return (ele.x === x && ele.y === y) || 
            (ele.x === x && ele.y === y-1)
        }).length
        // 地圖邊緣
        if (y === 0) ifOccupied++
      }
    
      if (ifOccupied) {
        console.log('不能蓋')
        throw false
      } else {
        console.log('可以蓋')
        return true
      }
    }
  }

  // 增加地圖大小
  tileScaleUp () {
    this.scene.tileGroup.destroy(true)
    userData.scale++
    this.buildMap('tiles', userData.scale)
  }

  // 生成地圖 tile
  buildMap (tileType, scale) {
    const tilewidth = 192
    const tileheight = 96
    const tileWidthHalf = tilewidth / 2
    const tileHeightHalf = tileheight / 2
    let mapScale = scale
    if (!scale) mapScale = userData.scale
    var centerX = w/2
    var centerY = h/2
    this.scene.tileGroup = this.scene.add.group()
    
    for (var y = 0; y < mapScale; y++){
      for (var x = 0; x < mapScale; x++){

        var tx = (x - y) * tileWidthHalf
        var ty = (x + y) * tileHeightHalf

        if ( this.scene.scene.key === 'HumanMainScene' && x === 3 ) {
          var tile = this.scene.add.image(centerX + tx, centerY + ty, 'tilesRiver').setOrigin(0.5,1)  
        } else {
          var tile = this.scene.add.image(centerX + tx, centerY + ty, tileType).setOrigin(0.5,1)
        }

        tile.depth = centerY + ty
        tile.coordinateX = x
        tile.coordinateY = y
        
        tile.setInteractive(this.scene.input.makePixelPerfect())
        tile.on('pointerdown', function(){
          console.log('mode', isOnBuildMode)
          if (((pointer.position.y > 140 && pointer.position.y < 576 )
            || (pointer.position.y < 140 && (pointer.position.x > 195 && pointer.position.x < 265) 
            || ((pointer.position.y > 92 && pointer.position.y < 140) && pointer.position.x > 265) ))) {
            Obj.lastCoordinateX = this.coordinateX
            Obj.lastCoordinateY = this.coordinateY  
            console.log(Obj.lastCoordinateX, Obj.lastCoordinateY)
            this.scene.tileGroup.getChildren().map( ele => { ele.clearTint() })
            this.scene.buildingGroup.getChildren().map( ele => { ele.clearTint() })
            // 建造模式打開變色調功能
            if (isOnBuildMode) {
              console.log(buildGrid)
              if (buildGrid.row === 1 && buildGrid.column === 1) {
                this.setTint(0xD0DDFC)
              } else if (buildGrid.row === 1 && buildGrid.column === 2) {
                this.setTint(0xD0DDFC)
                let gridIndex = this.scene.tileGroup.getChildren().findIndex(ele => { 
                  return ele.coordinateX === this.coordinateX && ele.coordinateY === this.coordinateY-1
                })
                if (gridIndex >= 0) this.scene.tileGroup.getChildren()[gridIndex].setTint(0xD0DDFC)
              } else if (buildGrid.row === 2 && buildGrid.column === 2) {
                this.setTint(0xD0DDFC)
                this.scene.tileGroup.getChildren().map(ele => {
                  if( 
                    (ele.coordinateX === this.coordinateX && ele.coordinateY === this.coordinateY-1)
                    || (ele.coordinateX === this.coordinateX-1 && ele.coordinateY === this.coordinateY)
                    || (ele.coordinateX === this.coordinateX-1 && ele.coordinateY === this.coordinateY-1)
                  ){ ele.setTint(0xD0DDFC) }
                })
              }
            }
          }
        })
        
        this.scene.tileGroup.add(tile)
      }
    }
  }

  // 拆除建築
  removeBuilding (buildingElement) {
    // 從 Phaser buildingGroup中拿掉
    let theRemoveBuilding = this.scene.buildingGroup.getChildren().find(ele => {
      return ele.coordinateX === buildingElement.coordinateX && ele.coordinateY === buildingElement.coordinateY
    })
    theRemoveBuilding.destroy(true)
  }

  // 搬移
  moveBuilding (ele, x, y) { // ele=要搬移之建築; x,y=移到的座標
    
  }

  // 取消建築彈跳
  cancelJump () {
    this.scene.buildingGroup.getChildren().map( ele => { ele.clearTint() } )
    if(Obj.lastTile){
      // this.scene.tweens.add({
      //     targets: Obj.lastTile,
      //     duration: 130,
      //     y: '+=20',
      //     ease: 'Quad.easeOut',
      // })
      Obj.lastTile.isJump = false
      Obj.lastTile = null
    }
  }

  // 地圖縮放
  zoomInOut (scale, duration, ease) {
    this.scene.cameras.main.zoomTo(scale, duration, ease);
  }
 
  setIsOnBuildMode (bool) {
    isOnBuildMode = bool
    this.scene.tileGroup.getChildren().map(ele => ele.clearTint())
    return isOnBuildMode
  }

  backToOrig () {
    let mapScale = userData.scale
    this.scene.cameras.main.pan(w/2, h/2+(mapScale-3)*48, 1000, 'Quart.easeOut')
  }

  changeScene (scene) {
    this.scene.scene.transition({
      target: scene,
      duration: 0,
      sleep: true
    })
    
    if (scene = 'PlanetScene') PlanetGameFunc.CameraFadeIn()
  }

  directlyBuild (thisBuilding) {
    userData.buildingMap.map((ele, index) => {
      if (ele.coordinateX === thisBuilding.coordinateX && ele.coordinateY === thisBuilding.coordinateY){
        this.scene.buildingGroup.getChildren().map(ele => {
          if (ele.coordinateX === thisBuilding.coordinateX && ele.coordinateY === thisBuilding.coordinateY) {
            clearTimeout(ele.timer)
            ele.destroy(true)
          }
        })
        userData.buildingMap[index].status = 'completed'
        this.createBuilding('b1', ele)
      }
    })
  }
}

// 將座標 x,y 轉換為遊戲世界座標
function positionAdjust (x, y, row, column) {
  let positionX, positionY
  const halfW = document.body.clientWidth/2
  const halfH = document.body.clientHeight/2
  const offsetWidth = 192/2
  const offsetHeight = 192/4
  if (row === 1 && column === 1 || row === 2 && column === 2) {
    positionX = halfW + x*offsetWidth - y*offsetWidth
    positionY = halfH + x*offsetHeight + y*offsetHeight
  } else if (row === 1 && column === 2) {
    console.log('adjust 1x2')
    positionX = halfW + x*offsetWidth - y*offsetWidth + 0.5*offsetWidth
    positionY = halfH + x*offsetHeight + y*offsetHeight
  } else if (row === 2 && column === 1) {
    console.log('adjust 2x1')
    // 還沒做
    positionX = halfW + x*offsetWidth - y*offsetWidth
    positionY = halfH + x*offsetHeight + y*offsetHeight
  } else if (row === 8 && column === 1) {
    console.log('adjust 8x1')
    positionX = halfW + x*offsetWidth - y*offsetWidth + 3.5*offsetWidth
    positionY = halfH + x*offsetHeight + y*offsetHeight
  } else if (row === 1 && column === 8) {
    positionX = halfW + x*offsetWidth - y*offsetWidth - 3.5*offsetWidth
    positionY = halfH + x*offsetHeight + y*offsetHeight
  }
  return { x:positionX, y:positionY }
}

// 取得建築資訊
function getData (id) {
  let type = id.split('')[0]
  let index = id.split('')[1]
  console.log(id)
  if (id.split('').length === 3) {
    index = parseInt(id.split('')[1].concat(id.split('')[2])) 
  }
  let data
  
  switch (type) {
    case 'c':
      data = buildingInfo.commonBuilding
      break
    case 'm':
      data = buildingInfo.militaryBuilding
      break
    case 'e':
      data = buildingInfo.economicBuilding
      break
    case 's':
      data = buildingInfo.specialBuilding
      break
    case 'k':
      data = buildingInfo.kyronusBuilding
      break
    case 'b':
      data = buildingInfo.buildProcess
    default:
      break
  }
  return data[index]
}

function setDelayTime (min) {
  let time = min * 60 * 1000
  let nowTime = Date.now()
  let finishTime = nowTime + time
  return finishTime
}